// Dependencies
import React, { useState, useCallback } from 'react'
import styled from 'styled-components/native'
import { Image } from 'react-native';
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'
import SnackBar from 'react-native-snackbar-component'

// Components
import Screen from '../../../components/screen'
import Button from '../../../components/button'
import ConvertIcon from '../../../assets/icons/convert-icon.png'
import SendIcon from '../../../assets/icons/transaction-receive-icon.png'
import ReceiveIcon from '../../../assets/icons/transaction-send-icon.png'
import BottomSheet from '../../../components/bottom-sheet';
import CoinsListSheet from '../../../components/coins-list-sheet';
import CoinHeader from '../../../components/exchange-header';
import Footer from "../../../components/footer";
import ScrollView from "../../../components/scroll-view";
import { STORE_KEYS } from '../../../stores'

// Dummy data
import data from '../wallets/data'

// Prepare performance sheet
const CurrencySheetMemo = React.memo(({ active, activeCoin, onClose }) => {
  return (
    <BottomSheet active={active} onClose={onClose} touchable={false}>
      <CoinsListSheet data={data[0].items} activeCoin={activeCoin} onCancel={onClose}/>
    </BottomSheet>
  )
})

// Export Component
const Exchange = ({ getBalance, setAmountOfExchange, navigation }) => {
  const [showCurrencies, setShowCurrencies] = useState(false)
  const [activeCoin, setActiveCoin] = useState('')
  const [fromCoin, setFromCoin] = useState(null)
  const [toCoin, setToCoin] = useState(null)
  const [snack, setSnack] = useState(false)
  const [snackMsg, setSnackMsg] = useState('')

  const right = {
    icon: 'menu',
    onPress: () => navigation.navigate('Settings')
  }

  const onCurrencySheetClose = useCallback((ret, activeMode) => {
    if (activeMode === 'left') {
      setFromCoin(ret);
    } else {
      setToCoin(ret);
    }
    setShowCurrencies(false)
  }, [])

  const onCancelEx = () => {
    setActiveCoin('left')
    setFromCoin(null)
    setToCoin(null)
  };

  const onConvertEx = () => {
    setAmountOfExchange('');
    if (getBalance(fromCoin.key) <= 0) {
      setSnackMsg(`Your ${fromCoin.key} balance is insufficient to convert.`);
      setSnack(true);
      setTimeout(() => {
        setSnackMsg('');
        setSnack(false);
        onCancelEx();
      }, 2000);
      return;
    }
    navigation.navigate('VerifyExchange', { data: { from: fromCoin.key, to: toCoin.key, image: fromCoin.image } });
    onCancelEx();
  };

  // Main Footer Buttons
  const footerButtons = [
    {
      onPress: () => onCancelEx(),
      label: 'Cancel'
    }, {
      onPress: () => onConvertEx(),
      label: 'Convert',
      primary: true
    }
  ]

  return (
    <Screen title="Exchange" {...{ navigation, right }} left={false}>
      <Container>
        <Content>
          <ScrollView hasFooter>
            {!fromCoin ? (
              <ButtonWrapper>
                <Button
                  label="Select Source Wallet"
                  onPress={() => {
                    setActiveCoin('left');
                    setShowCurrencies(true);
                  }}
                  after={<Image source={SendIcon} style={{ width: 15, height: 14, marginTop: 2 }}/>}
                  primary large
                />
              </ButtonWrapper>
            ) : (
              <CoinHeader image={fromCoin.image} title={fromCoin.title} amount={fromCoin.coinAmount}/>
            )}
            <ArrowWrapper>
              <Image source={ConvertIcon} style={{ width: 30, height: 34, marginTop: 2 }}/>
            </ArrowWrapper>
            {!toCoin ? (
              <ButtonWrapper>
                <Button
                  label="Select Receiving Wallet"
                  onPress={() => {
                    setActiveCoin('right');
                    setShowCurrencies(true);
                  }}
                  after={<Image source={ReceiveIcon} style={{ width: 15, height: 14, marginTop: 2 }}/>}
                  large
                />
              </ButtonWrapper>
            ): (
              <CoinHeader image={toCoin.image} title={toCoin.title} amount={toCoin.coinAmount}/>
            )}
          </ScrollView>
        </Content>
        {toCoin && fromCoin && (
          <Footer buttons={footerButtons} />
        )}
      </Container>
      <CurrencySheetMemo active={showCurrencies} activeCoin={activeCoin} onClose={onCurrencySheetClose}/>
      <SnackBar
        visible={snack}
        textMessage={snackMsg}
      />
    </Screen>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         getBalance,
         setAmountOfExchange,
       }
     }) => ({
      getBalance,
      setAmountOfExchange,
    })
  )
)(Exchange);

// Styles
const Container = styled.View`
  flex: 1; 
`

const Content = styled.View`
  width: 100%;
  height: 100%;
  margin-bottom: auto;
  padding: ${props => props.theme.spacing}px ${props => props.theme.spacing * 2}px;
`

const ArrowWrapper = styled.View`
  width: 100%;
  display: flex;
  align-items: center;
  margin: 10px 0;
`

const ButtonWrapper = styled.View`
  width: 100%;
  height: 60px;
  margin-left: auto;
  margin-right: auto;
`
