// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'
import Base64 from 'Base64';
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../../components/screen'
import TextInputField from "../../../components/text-input-field";
import { Body } from "../../../components/typography";
import { STORE_KEYS } from '../../../stores'
import Button from "../../../components/button";

// Export component
const VerifyExchangge = ({ verifyTx, navigation }) => {
  const itemData = navigation.getParam('data', {});
  const [pass, setPass] = useState('')
  const [error, setError] = useState('')

  const onVerifyExchangge = () => {
    if ((pass || '').trim().length === 0) {
      setError('Verification is failed.');
      return;
    }
    verifyTx(Base64.btoa(pass))
      .then(success => {
        if (success) {
          navigation.navigate('ExAmountConfirm', { data: { ...itemData, code: Base64.btoa(pass) } })
        } else {
          setError('Verification is failed.');
        }
      })
      .catch(() => {
        setError('Verification is failed.');
      });
  };

  return (
    <Wrapper behavior="padding" enabled>
      <Screen title={'Verify Exchange'} {...{ navigation }}>
        <Container>
          <Top>
            <Label>Enter your password</Label>
            <TextInputField
              secureTextEntry={true}
              onChangeText={setPass}
              value={pass}
            />
            {(error || '').trim().length > 0 && (
              <Error>{ error }</Error>
            )}
            <Description>
              Your wallet is encrypted on your device with your personal password.
              Your password acts as your decryption key to both lock and unlock your wallet — your wallet cannot be accessed without it.
            </Description>
          </Top>
          <Bottom>
            <Button label="Verify" onPress={() => onVerifyExchangge()} primary large/>
          </Bottom>
        </Container>
      </Screen>
    </Wrapper>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         addressOfSend,
         setAddressOfSend,
         verifyTx,
       }
     }) => ({
      addressOfSend,
      setAddressOfSend,
      verifyTx,
    })
  )
)(VerifyExchangge);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px ${props => props.theme.spacing}px 0;
  justify-content: space-between;
`

const Top = styled.View`
`

const Bottom = styled.View`
  height: 50px;
  margin-bottom: ${props => props.theme.spacing}px;
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
