// Dependencies
import React, { Fragment } from 'react'
import styled from 'styled-components/native'
import { View } from 'react-native'
import { Ionicons } from '@expo/vector-icons'
import Constants from 'expo-constants'
import theme from '../../theme'

// Components
import TouchableOpacity from '../touchable-opacity'
import TextInputField from '../text-input-field'
import TabBarIcon from '../TabBarIcon';

// Get statusbar height
const { statusBarHeight } = Constants

// Export component
export default ({ title, navigation, input, right = false, left = true }) => {
  if (!navigation) {
    return <View style={{ height: statusBarHeight }}/>
  }

  const renderRightSection = ({ icon, ...props }) => {
    return (
      <IconWrapper as={TouchableOpacity} {...props}>
        {icon === 'info' && <InfoIcon source={require('./assets/info.png')}/>}
        {icon === 'detail' && <DetailIcon source={require('../../assets/icons/three-dot-icon.png')}/>}
        {icon === 'menu' && <TabBarIcon name="md-menu" />}
      </IconWrapper>
    )
  }

  return(
    <Container>
      <Left>
        {!!left && (
          <IconWrapper as={TouchableOpacity} onPress={() => navigation.pop()}>
            {navigation.isFirstRouteInParent()
              ? <Ionicons name="ios-close" size={42} color="white" />
              : <Ionicons name="ios-arrow-back" size={28} color="white" />
            }
          </IconWrapper>
        )}
      </Left>
      {!!input && (
        <SearchInput placeholderTextColor={theme.colors.light} {...input} />
      )}
      {!input && (
        <Fragment>
          <Center>
            <Title>{title}</Title>
          </Center>
          <Right>
            {!!right && renderRightSection(right)}
          </Right>
        </Fragment>
      )}
    </Container>
  )
}

// Styles
const Container = styled.View`
  margin-top: ${statusBarHeight}px;
  flex-direction: row;
  padding: 20px;
`

const Title = styled.Text`
  color: #fff;
  font-size: 20px;
  text-align: center;
  margin: 0 10px;
`

const Center = styled.View`
  flex: 1;
  justify-content: center;
  align-items: center;
`

const Left = styled.View`
  width: ${props => props.theme.touchTargetSize}px;
  justify-content: center;
  align-items: flex-start;
`

const Right = styled.View`
  width: ${props => props.theme.touchTargetSize}px;
  justify-content: center;
  align-items: flex-end;
`

const IconWrapper = styled.View`
  width: ${props => props.theme.touchTargetSize}px;
  justify-content: center;
  align-items: center;
`

const InfoIcon = styled.Image`
  margin-top: 4px;
  width: 20px;
  height: 20px;
`

const DetailIcon = styled.Image`
  width: 18px;
  height: 4px;
`

const SearchInput = styled(TextInputField)`
  flex: 1;
  font-size: 14px;
  line-height: 19px;
  height: ${props => props.theme.touchTargetSize}px;
  background-color: ${props => props.theme.colors.dim};
`
