// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../../components/screen'
import Button from '../../../components/button'
import { Body } from '../../../components/typography'
import { STORE_KEYS } from '../../../stores'
import Progress from '../../../components/progress'

// Export component
const ConfirmAccount = ({ navigation, registerWithUsername, loading }) => {
  const [err, setErr] = useState('')

  const onMoveNext = () => {
    registerWithUsername()
      .then(success => {
        if (success) {
          navigation.navigate('OnboardingSign')
        } else {
          setErr('Sorry, your registration is failed.')
        }
      })
  }

  return (
    <Wrapper behavior="padding" enabled>
      <Screen title={'Last Step'} {...{ navigation }}>
        <Container>
          <Top>
            <Description>
              Let's finish with a quick review
            </Description>
            <Agreement>
              1. I understand I'm responsible for my funds, and that pWallet only has an encrypted copy for recovery purposes.{"\n"}{"\n"}
              2. I understand that if I lose this device or uninstall the app, my digital assets can only be recovered with my username and password{"\n"}{"\n"}
              3. I understand that if I lose my username and password, pWallet will not be able to recover my account, unless I setup password recovery{"\n"}{"\n"}
              {(err || '').length > 0 && (
                <Error>{ err }</Error>
              )}
            </Agreement>
          </Top>
          <Bottom>
            <Button label="Confirm & Finish" onPress={onMoveNext} primary large style={{ width: '100%' }} />
          </Bottom>
        </Container>
      </Screen>
      { loading && <Progress done={false}/> }
    </Wrapper>
  )
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: {
         registerWithUsername,
         loading,
       }
     }) => ({
      registerWithUsername,
      loading,
    })
  )
)(ConfirmAccount);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const InputWrapper = styled.View`
  margin-bottom: 10px;
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .2}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
  text-align: center;
`

const Agreement = styled(Body)`
  padding: 10px;  
  margin-top: 10px;
  font-weight: bold;
  text-align: left;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 10px;  
`;
