// Dependencies
import React, { useState, Component } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../components/screen'
import Button from '../../components/button'
import TextInputField from '../../components/text-input-field'
import { Body } from '../../components/typography'
import { STORE_KEYS } from '../../stores'
import Progress from '../../components/progress'

class ChangePass extends Component {
  constructor(props) {
    super(props);

    this.state = {
      password: '',
      error: '',
    }
  }

  onUpdatePass = () => {
    const { password, newPass, newPassAgain } = this.state;
    const { changePass, navigation } = this.props;

    if ((password || '').trim().length === 0) {
      this.setState({
        error: "Please input password correctly."
      });
      return;
    }

    if ((newPass || '').trim().length === 0) {
      this.setState({
        error: "Please input password correctly."
      });
      return;
    }

    if ((newPassAgain || '').trim().length === 0) {
      this.setState({
        error: "Please input password correctly."
      });
      return;
    }

    if (newPass !== newPassAgain) {
      this.setState({
        error: "Passwords are not matching."
      });
      return;
    }

    changePass(password, newPass)
      .then(success => {
        if (success) {
          navigation.navigate('SettingsMain')
        } else {
          this.setState({
            error: "Changing password is failed."
          })
        }
      })
      .catch(err => {
        this.setState({
          error: "Changing password is failed."
        })
      });
  };

  onChangePass = (value) => {
    this.setState({
      password: value,
    });
  };

  onChangeNewPass = (value) => {
    this.setState({
      newPass: value,
    });
  };

  onChangeNewPassAgain = (value) => {
    this.setState({
      newPassAgain: value,
    });
  };

  render() {
    const { error, password, newPass, newPassAgain } = this.state;
    const { navigation, loading } = this.props;

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Change Password'} {...{ navigation }}>
          <Container>
            <Top>
              <Label top>Current Password</Label>
              <TextInputField
                secureTextEntry={true}
                onChangeText={this.onChangePass}
                value={password}
              />
              <Label>New Password</Label>
              <TextInputField
                secureTextEntry={true}
                onChangeText={this.onChangeNewPass}
                value={newPass}
              />
              <Label>New Password Again</Label>
              <TextInputField
                secureTextEntry={true}
                onChangeText={this.onChangeNewPassAgain}
                value={newPassAgain}
              />
              {(error || '').trim().length > 0 && (
                <Error>{ error }</Error>
              )}
            </Top>
            <Bottom>
              <Button label="UPDATE" onPress={this.onUpdatePass} primary large style={{ width: '100%' }} />
            </Bottom>
          </Container>
        </Screen>
        { loading && <Progress done={false}/> }
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { changePass, loading },
     }) => ({
      changePass,
      loading,
    })
  )
)(ChangePass);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .2}px;
  margin-top: ${props => !props.top ? props.theme.spacing * .8 : 0}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
