import { observable, action } from 'mobx';
import Base64 from 'Base64';

import {
  registerWithName, loginWithName, checkUsernameExist, checkPassFor2FA,
  GetSecurityRequest, Enable2FARequest, Verify2FARequest, Disable2FARequest,
  BackupWalletRequest, ChangePasswordRequest, LoginWithPinRequest, ChangePinRequest
} from "../lib/apis";
import { _retrieveData, _storeData, _clear } from '../utils';

class AuthorityStore {
  @observable isLoggedIn = false;

  @observable username = '';
  @observable password = '';
  @observable password02 = '';
  @observable pincode = '';
  @observable loading = false;
  @observable dataUrl = '';
  @observable is2FAEnabled = false;

  constructor() {
    this.initStore();
  }

  async initStore() {
    const authToken = await _retrieveData('authToken');
    this.isLoggedIn = !!authToken; // !!localStorage.getItem('authToken');
    this.username = await _retrieveData('username'); // localStorage.getItem('username');
  }

  // API interaction
  @action.bound async loginWithUsername(username, password) {
    this.loading = true;
    await this.clearStorage();
    return new Promise((resolve, reject) => {
      loginWithName(username, password)
        .then(async data => {
          this.username = data.username || '';
          this.is2FAEnabled = data.is_tfa;
          await _storeData('authToken', data.jwt || ""); // localStorage.setItem("authToken", data.jwt || "");
          await _storeData('username', this.username); // localStorage.setItem("username", this.username);
          this.isLoggedIn = true;
          this.loading = false;
          resolve({
            success: true,
            is_tfa: data.is_tfa,
          });
        })
        .catch(e => {
          // console.log('[verify failed]', e);
          this.loading = false;
          resolve({
            success: false,
            is_tfa: false,
          });
        });
    });
  }

  @action.bound registerWithUsername() {
    this.loading = true;
    return new Promise((resolve, reject) => {
      registerWithName(this.username, this.password, this.pincode)
        .then(data => {
          this.loading = false;
          this.password = '';
          this.password02 = '';
          resolve(data.success); // true or false
        })
        .catch(e => {
          this.loading = false;
          resolve(false);
        });
    });
  }

  @action.bound checkUsername(username) {
    this.loading = true;
    return new Promise((resolve, reject) => {
      checkUsernameExist(username)
        .then(data => {
          this.loading = false;
          if (data.success) {
            resolve(false);
          } else {
            resolve(true);
          }
        })
        .catch(error => {
          resolve(false);
        })
    });
  }

  @action.bound checkPass(pass) {
    // console.log('password:', pass);
    this.loading = true;
    return new Promise((resolve, reject) => {
      checkPassFor2FA(this.username, pass)
        .then(data => {
          this.loading = false;
          if (data.success) {
            resolve(true);
          } else {
            resolve(false);
          }
        })
        .catch(error => {
          resolve(false);
        })
    });
  }

  /**
   *  Logout
   */
  async clearStorage() {
    this.isLoggedIn = false;
    this.username = '';
    await _clear(); // localStorage.clear();
  }
  @action.bound async logout() {
    await this.clearStorage();
  }

  // Component interaction
  @action.bound init() {
    this.username = '';
    this.password = '';
    this.password02 = '';
    this.pincode = '';
  }

  @action.bound setUsername(name) {
    this.username = name;
  }

  @action.bound setPassword(pass) {
    this.password = pass;
  }

  @action.bound setPassword02(pass02) {
    this.password02 = pass02;
  }

  @action.bound setPincode(code) {
    this.pincode = code;
  }

  /**
   *  Security
   */
  @action.bound async getSecurityRequest() {
    const authToken = await _retrieveData('authToken');
    return new Promise((resolve, reject) => {
      GetSecurityRequest(authToken)
        .then(data => {
          // console.log('data:', data);
          if (!data.error) {
            // console.log(data.message);
          } else {
            // console.log(data.error);
          }
          resolve(true);
        })
        .catch(err => {
          reject("Failed.");
        })
    });
  }

  @action.bound async enable2FA() {
    const authToken = await _retrieveData('authToken');
    return new Promise((resolve, reject) => {
      Enable2FARequest(authToken)
        .then(data => {
          // console.log('data:', data);
          if (!data.error) {
            // console.log(data.message);
            this.dataUrl = data.dataUrl || '';
          } else {
            // console.log(data.error);
            this.dataUrl = '';
          }
          resolve(true);
        })
        .catch(err => {
          this.dataUrl = '';
          reject("Failed.");
        })
    });
  }

  @action.bound async verify2FA(confirm_number) {
    const authToken = await _retrieveData('authToken');
    return new Promise((resolve, reject) => {
      Verify2FARequest(authToken, { confirm_number })
        .then(data => {
          // console.log('data:', data);
          if (!data.error) {
            // console.log(data.message);
            this.is2FAEnabled = true;
            resolve(true);
          } else {
            // console.log(data.error);
            resolve(false);
          }
        })
        .catch(err => {
          reject("Failed.");
        })
    });
  }

  @action.bound async disable2FA() {
    const authToken = await _retrieveData('authToken');
    return new Promise((resolve, reject) => {
      Disable2FARequest(authToken)
        .then(data => {
          // console.log('data:', data);
          if (!data.error) {
            // console.log(data.message);
          } else {
            // console.log(data.error);
          }
          resolve(true);
        })
        .catch(err => {
          reject("Failed.");
        })
    });
  }

  // version 2
  @action.bound async backupWallet(pass) {
    this.loading = true;
    const authToken = await _retrieveData('authToken');
    return new Promise((resolve, reject) => {
      BackupWalletRequest(authToken, { code: Base64.btoa(pass) })
        .then(data => {
          // console.log('data:', data);
          const success = data.success;
          this.loading = false;
          if (success) {
            resolve(data.mnemonic || '');
          } else {
            resolve('');
          }
        })
        .catch(err => {
          this.loading = false;
          reject("Failed.");
        })
    });
  }

  @action.bound async changePass(pass, newPass) {
    this.loading = true;
    const authToken = await _retrieveData('authToken');
    return new Promise((resolve, reject) => {
      ChangePasswordRequest(authToken, {
        code: Base64.btoa(pass),
        newCode: Base64.btoa(newPass),
      })
        .then(data => {
          // console.log('data:', data);
          const success = data.success;
          this.loading = false;
          resolve(success);
        })
        .catch(err => {
          this.loading = false;
          reject("Failed.");
        })
    });
  }

  @action.bound async loginWithPin(pincode) {
    this.loading = true;
    const authToken = await _retrieveData('authToken');
    return new Promise((resolve, reject) => {
      LoginWithPinRequest(authToken, {
        pincode,
      })
        .then(data => {
          // console.log('data:', data);
          const success = data.success;
          this.loading = false;
          resolve(success);
        })
        .catch(err => {
          this.loading = false;
          reject("Failed.");
        })
    });
  }

  @action.bound async changePin(pincode) {
    const authToken = await _retrieveData('authToken');
    this.loading = true;
    return new Promise((resolve, reject) => {
      ChangePinRequest(authToken, {
        pincode,
      })
        .then(data => {
          // console.log('data:', data);
          const success = data.success;
          this.loading = false;
          resolve(success);
        })
        .catch(err => {
          this.loading = false;
          reject("Failed.");
        })
    });
  }
}

export default () => new AuthorityStore();
