import axios from "axios";

const BACKEND_SERVER = 'https://mobile.bct.link';
const getHeaders = token => ({
  headers: {
    Authorization: `Bearer ${token}`,
    'Content-Type': 'application/json'
  }
});
export const loginWithName = (username, password) =>
  axios.post(`${BACKEND_SERVER}/api/auth/loginUser`, { username, password }, getHeaders())
    .then(({ data }) => data)
    .catch((err) => Promise.reject(err.response.data));

