// Dependencies
import React, { Fragment } from 'react'
import { Provider } from 'mobx-react';
import styled from 'styled-components/native'
import { Platform, StatusBar } from 'react-native'
import { registerRootComponent, SplashScreen } from 'expo'
import { ThemeProvider } from 'styled-components/native'
import { screensEnabled } from 'react-native-screens'
import RootNavigation from './navigation'
import Stores from './stores';
import theme from './theme'

// Use native navigation container components
screensEnabled()

// Keep the splash screen visible until manually hidden
SplashScreen.preventAutoHide()

// Bootstrap project
const App = () => {
  const isMobile = Platform.OS === 'ios' || Platform.OS === 'android';
  return (
    <Provider {...Stores()}>
      <ThemeProvider {...{ theme }}>
        <Fragment>
          <StatusBar barStyle="light-content"/>
          <Container isMobile={isMobile}>
            <Gradient fadeDuration={0} resizeMode="stretch" source={require('./assets/gradient.png')}/>
            <RootNavigation/>
          </Container>
        </Fragment>
      </ThemeProvider>
    </Provider>
  )
}

// Styles
const Container = styled.View`
  flex: 1;
  background-color: ${props => props.theme.colors.background};
  ${props => !props.isMobile && `
    min-width: 500px;
    margin: auto;
    width: 30%;
  `};
`

const Gradient = styled.Image`
  width: 100%;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
`

// Expo: Register root component
registerRootComponent(App)
