// Dependencies
import React from 'react'
import styled from 'styled-components/native'

// Components
import { Heading } from '../typography'
import TouchableOpacity from '../touchable-opacity'
import screen from '../../constants/Layout'
import ScrollView from '../../components/scroll-view'

// Export component
export default ({ data = [], activeCoin, onCancel }) => {
  return (
    <Container height={screen.window.height || 0}>
      <ScrollView>
        {
          data.map((item, index) => (
            <Item onPress={() => onCancel(item, activeCoin)} key={index}>
              <ItemImage source={item.image}/>
              <ItemName>{`${item.key} - ${item.title}`}</ItemName>
            </Item>
          ))
        }
      </ScrollView>
    </Container>
  )
}

// Styles
const Container = styled.View`
  flex: 1;
  height: ${props => (props.height || 0) * 0.5}px;
`

const Item = styled(TouchableOpacity)`
  flex-direction: row;
  align-items: center;
  height: 60px;
`

const ItemImage = styled.Image`
  width: 36px;
  height: 36px;
  border-radius: 18px;
`

const ItemName = styled(Heading)`
  padding: 0 ${props => props.theme.spacing * .5}px;
  font-size: 16px;
  line-height: 24px;
`
