// Dependencies
import React, { useState, Component } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../components/screen'
import Button from '../../components/button'
import TextInputField from '../../components/text-input-field'
import { Body } from '../../components/typography'
import { STORE_KEYS } from '../../stores'
import Progress from '../../components/progress'

class ChangePin extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pincode: '',
      error: '',
    }
  }

  onActChangePin = () => {
    const { pincode } = this.state;
    const { changePin, navigation } = this.props;
    if ((pincode || '').trim().length > 0) {
      this.setState({
        error: '',
      });

      changePin(pincode)
        .then(success => {
          if (success) {
            navigation.navigate('SettingsMain')
          } else {
            this.setState({
              error: "Updating Pincode is Failed."
            })
          }
        })
        .catch(err => {
          this.setState({
            error: "Updating Pincode is Failed."
          })
        })
    } else {
      this.setState({
        error: "Please input pincode correctly."
      })
    }
  };

  onChangePin = (value) => {
    this.setState({
      pincode: value,
    });
  };

  render() {
    const { error, pincode } = this.state
    const { navigation, loading } = this.props

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Change Pin'} {...{ navigation }}>
          <Container>
            <Top>
              <Label>New PinCode</Label>
              <TextInputField
                keyboardType='numeric'
                onChangeText={this.onChangePin}
                value={pincode}
              />
              <Description>
                Please input new 4 digits.
              </Description>
              {(error || '').trim().length > 0 && (
                <Error>{ error }</Error>
              )}
            </Top>
            <Bottom>
              <Button label="Update" onPress={this.onActChangePin} primary large style={{ width: '100%' }} />
            </Bottom>
          </Container>
        </Screen>
        { loading && <Progress done={false}/> }
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { changePin, loading },
     }) => ({
      changePin,
      loading,
    })
  )
)(ChangePin);


// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
