// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../components/screen'
import Header from '../../components/settings-header'
import ScrollView from '../../components/scroll-view'
import { Section, Item } from '../../components/settings-list'
import { STORE_KEYS } from '../../stores'

// Export component
const SettingsCom = ({ logout, username, is2FAEnabled, navigation }) => {
  const [pinLogin, enablePinLogin] =  useState(false)
  const [sound, enableSound] =  useState(true)

  const _logout = () => {
    navigation.navigate('OnboardingSign');
    logout();
  };

  const set2FAProcess = () => {
    if (!is2FAEnabled) {
      navigation.navigate('TfaConfirm');
    }
  };

  const items = [
    {
      title: "Account",
      items: [
        {
          onPress: () => {
            navigation.navigate('ChangePass');
          },
          label: "Change Password",
          icon: require('../../assets/icons/user-icon.png')
        }, {
          onPress: () => {
            navigation.navigate('ChangePin');
          },
          label: "Change PIN",
          icon: require('../../assets/icons/pin-icon.png')
        }, {
          label: "Setup 2 Factor",
          type: "toggle",
          value: is2FAEnabled,
          onChange: set2FAProcess,
          icon: require('../../assets/icons/pair-icon.png')
        // }, {
        //   onPress: () => {},
        //   label: "Set Up Password Recovery",
        //   icon: require('../../assets/icons/user-icon.png')
        }, {
          onPress: () => {
            navigation.navigate('BackupWallet');
          },
          label: "Backup Wallet",
          icon: require('../../assets/icons/backup-wallet-icon.png')
        }, {
          label: "Log in with PIN",
          type: "toggle",
          value: pinLogin,
          onChange: enablePinLogin,
          icon: require('../../assets/icons/login-pin-icon.png')
        }
      ]
    }, {
      title: "Preferences",
      items: [
        {
          onPress: () => {},
          label: "Notifications",
          icon: require('../../assets/icons/notification-icon.png')
        }, {
          label: "Sound",
          type: "toggle",
          value: sound,
          onChange: enableSound,
          icon: require('../../assets/icons/sound-icon.png')
        }, {
          onPress: () => {},
          label: "Language",
          value: "English",
          icon: require('../../assets/icons/language-icon.png')
        }, {
          onPress: () => {},
          label: "Currency",
          value: "USD",
          icon: require('../../assets/icons/currency-icon.png')
        }
      ]
    }, {
      title: "",
      items: [
        {
          onPress: _logout,
          label: "Log out",
          icon: require('../../assets/icons/power-icon.png')
        }
      ]
    }
  ];

  return (
    <Screen title="Settings" {...{ navigation }}>
      <ScrollView>
        <Header name={username}/>
        <Settings>
          {
            items.map(section => (
              <Section
                title={section.title}
                key={section.title}
              >
                {
                  section.items.map((item, index) => (
                    <Item
                      {...item}
                      key={index}
                    />
                  ))
                }
              </Section>
            ))
          }
        </Settings>
      </ScrollView>
    </Screen>
  )
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: {
         logout, username, is2FAEnabled,
       }
     }) => ({
      logout, username, is2FAEnabled,
    })
  )
)(SettingsCom);

// Styles
const Settings = styled(ScrollView)`
  margin-top: 0;
`
