// Dependencies
import React, { useState, Component } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../components/screen'
import Button from '../../components/button'
import TextInputField from '../../components/text-input-field'
import { Body } from '../../components/typography'
import { STORE_KEYS } from '../../stores'
import Progress from '../../components/progress'

class BackupWallet extends Component {
  constructor(props) {
    super(props);

    this.state = {
      password: '',
      error: '',
      isBackUp: false,
      phrase: '',
    }
  }

  onActDone = () => {
    const { navigation } = this.props;
    navigation.navigate('SettingsMain');
  };

  onActBackup = () => {
    const { password } = this.state;
    const { backupWallet } = this.props;
    if ((password || '').trim().length > 0) {
      this.setState({
        error: '',
      });

      backupWallet(password)
        .then(data => {
          this.setState({
            isBackUp: true,
            phrase: data,
          });
        })
        .catch(err => {
          this.setState({
            error: "Password verification is failed."
          })
        })
    } else {
      this.setState({
        isValid: false,
        error: "Please input password correctly."
      })
    }
  };

  onChangePass = (value) => {
    this.setState({
      password: value,
    });
  };

  render() {
    const { error, password, isBackUp, phrase } = this.state
    const { navigation, loading } = this.props

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Backup Wallet'} {...{ navigation }}>
          <Container>
            {!isBackUp ? (
              <Top>
                <Label>Password</Label>
                <TextInputField
                  secureTextEntry={true}
                  onChangeText={this.onChangePass}
                  value={password}
                />
                {(error || '').trim().length > 0 && (
                  <Error>{ error }</Error>
                )}
                <Description top>
                  Please input your password and backup phrase will be given after validation.
                </Description>
              </Top>
            ) : (
              <Top>
                <Description>
                  Please write down a 24-word Backup Phrase
                  and keep the copy in a secure place
                </Description>
                <LabelLg>{phrase}</LabelLg>
              </Top>
            )}
            <Bottom>
              {!isBackUp ? (
                <Button label="Enable" onPress={this.onActBackup} primary large style={{ width: '100%' }} />
              ) : (
                <Button label="Done" onPress={this.onActDone} primary large style={{ width: '100%' }} />
              )}
            </Bottom>
          </Container>
        </Screen>
        { loading && <Progress done={false}/> }
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { backupWallet, loading },
     }) => ({
      backupWallet,
      loading,
    })
  )
)(BackupWallet);


// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const LabelLg = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 15px;
  margin-bottom: ${props => props.theme.spacing * .8}px;
  text-align: center;
  background: #333;
  padding: 10px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-bottom: ${props => props.theme.spacing * .8}px;
  margin-top: ${props => props.top ? props.theme.spacing * .8 : 0}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
