// Dependencies
import React, { useState } from 'react'
import { Linking } from 'expo';
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'
import styled from 'styled-components/native'
import { Image, Platform } from "react-native";
import SnackBar from "react-native-snackbar-component";

// Components
import Screen from '../../components/screen'
import ScrollView from '../../components/scroll-view'
import Footer from '../../components/footer'
import CoinHeader from "../../components/coin-header";
import { Section, Item } from '../../components/transaction-detail'
import { STORE_KEYS } from '../../stores'
import { commafyDigitFormat } from '../../utils';

// Images
import ReceiveIcon from '../../assets/icons/receive-icon.png'
import SendIcon from '../../assets/icons/send-icon.png'

// Export component
const CoinDash = ({ getMarketObj, navigation }) => {
  const item = navigation.getParam('transaction', {});
  const [snack, setSnack] = useState(false);
  const [msg, setMsg] = useState('');

  // Main Footer Buttons
  const footerButtons = [
    {
      onPress: () => navigation.navigate('Receipt', { data: { ...item } }),
      after: <Image source={ReceiveIcon} style={{ width: 12, height: 12, marginTop: 1 }}/>,
      label: 'Request'
    }, {
      onPress: () => {
        const balance = item.balance || 0;
        if (balance <= 0) {
          setMsg('Your balance is insufficient to transfer.')
          setSnack(true)
          setTimeout(() => {
            setSnack(false)
          }, 2000)
        } else {
          navigation.navigate('SendMain', { data: { ...item } })
        }
      },
      after: <Image source={SendIcon} style={{ width: 15, height: 14, marginTop: 2 }}/>,
      label: 'Send',
      primary: true
    }
  ];

  const coinInfo = getMarketObj(item.key);

  let marketCap = coinInfo.price * coinInfo.supply;
  marketCap = marketCap ? '$' + commafyDigitFormat(marketCap, 8) : '-';

  let pegnetPrice = coinInfo.price;
  pegnetPrice = pegnetPrice ? '$' + commafyDigitFormat(pegnetPrice, 8) : '-';

  let exchangePrice = coinInfo.exchange_price;
  exchangePrice = exchangePrice ? '$' + commafyDigitFormat(exchangePrice, 8) : '-';

  let totalVolume = coinInfo.volume_price;
  totalVolume = totalVolume ? '$' + commafyDigitFormat(totalVolume, 8) : '-';

  let supply = coinInfo.supply;
  supply = supply ? commafyDigitFormat(supply, 5) + ' ' + coinInfo.ticker_symbol : '-';

  const items = [
    {
      label: '· Market Cap',
      value: marketCap,
    },
    {
      label: '· PegNet Price',
      value: pegnetPrice,
    },
    {
      label: '· Exchange Price',
      value: exchangePrice,
    },
    {
      label: '· Total Volume(24h)',
      value: totalVolume,
    },
    {
      label: '· Supply',
      value: supply,
    },
    {
      label: '· More details',
      value: 'View Market Cap',
      onPress: () => {
        const isMobile = Platform.OS === 'ios' || Platform.OS === 'android';
        if (isMobile) {
          Linking.openURL('https://pegnetmarketcap.com');
        } else {
          window.open("https://pegnetmarketcap.com", "_blank");
        }
      },
    }
  ]

  return (
    <Screen title={"Balance"} {...{ navigation }}>
      <Container>
        <ScrollView hasFooter>
          <CoinHeader image={item.image} title={item.title} amount={item.coinAmount}/>
          <Section title="Details">
            {items.map((item, index) => (
              <Item {...item} key={index} />
            ))}
          </Section>
        </ScrollView>
        <SnackBar
          visible={snack}
          textMessage={msg}
        />
        <Footer buttons={footerButtons} />
      </Container>
    </Screen>
  )
}

export default compose(
  inject(STORE_KEYS.COINPRICESTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.COINPRICESTORE]: {
         getMarketObj,
       }
     }) => ({
      getMarketObj,
    })
  )
)(CoinDash);

// Styles
const Container = styled.View`
  flex: 1;
`
