// Dependencies
import React, { useState, useEffect, Fragment, useCallback } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../components/screen'
import EnterAmountScreen from '../../components/enter-amount-screen'
import { STORE_KEYS } from '../../stores'

// Export component
const Send = ({ setAmountCoin, setAddressOfSend, navigation }) => {
  const itemData = navigation.getParam('data', {});
  const [ready, setReady] = useState(false)
  const recipient = navigation.getParam('recipient', false)
  const balance = itemData.balance || 0;

  useEffect(() => {
    setReady(true)
  }, [])

  return (
    <Fragment>
      <Screen title="Send" {...{ navigation }}>
        <Container>
          {ready && (
            <EnterAmountScreen
              onNext={({ amount, usdAmount, coin }) => {
                if (amount > 0 && amount <= balance) {
                  setAddressOfSend('');
                  setAmountCoin(amount, coin);
                  return navigation.navigate('SendRecipient', { data: { ...itemData } });
                }
              }}
              onRecipient={() => navigation.navigate('SendRecipient', { data: { ...itemData } })}
              onRemoveRecipient={() => navigation.setParams({ recipient: false })}
              {...{ recipient }}
              {...{ itemData }}
            />
          )}
        </Container>
      </Screen>
    </Fragment>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         setAmountCoin,
         setAddressOfSend,
       }
     }) => ({
      setAmountCoin,
      setAddressOfSend,
    })
  )
)(Send);

// Styles
const Container = styled.View`
  flex: 1;
`
