// Dependencies
import React, { useState } from 'react'
import styled, { css } from 'styled-components/native'
import Animated, { Easing } from 'react-native-reanimated'
import { timing } from 'react-native-redash'

// Prepare animated
const { Value, Clock, useCode, set, interpolate, Extrapolate, cond, neq } = Animated

// Constants
const DOT_SIZE = 40
const DOT_SIZE_SCALED = 30
const DOT_BORDER_WIDTH = 2

// Dot component
const Dot = React.memo(({ active, filled }) => {
  const [{ animation, clock }] = useState({
    animation: new Value(0),
    clock: new Clock()
  })

  useCode(
    () => cond(
      neq(animation, filled ? 1 : 0),
      set(
        animation,
        timing({
          clock,
          from: animation,
          to: filled ? 1 : 0,
          duration: 250,
          easing: Easing.linear
        })
      )
    ),
    [filled]
  )

  return (
    <Circle
      style={{
        transform: [
          {
            scale: interpolate(animation, {
              inputRange: [0, 1],
              outputRange: [1, DOT_SIZE_SCALED / DOT_SIZE],
              extrapolate: Extrapolate.CLAMP
            })
          }
        ]
      }}
      active={filled || active}
    >
      <InnerCircle
        style={{
          opacity: interpolate(animation, {
            inputRange: [0, 1],
            outputRange: [0, 1],
            extrapolate: Extrapolate.CLAMP
          })
        }}
      />
    </Circle>
  )
})

// Export component
export default ({ length = 6, value, ...props }) => {
  return (
    <Container {...props}>
      {[...new Array(length)].map((_, index) => {
        return (
          <Dot
            key={`dot-${index}`}
            filled={index < value.length}
            active={index === value.length}
          />
        )
      })}
    </Container>
  )
}

// Styles
const Container = styled.View`
  flex-direction: row;
`

const Circle = styled(Animated.View)`
  width: ${DOT_SIZE}px;
  height: ${DOT_SIZE}px;
  border-radius: ${DOT_SIZE * .5}px;
  border: ${DOT_BORDER_WIDTH}px solid ${props => props.theme.colors.tertiary};
  margin: 0 ${props => props.theme.spacing * .5}px;
  background-color: ${props => props.theme.colors.tertiary};

  ${props => props.active && css`
    border-color: ${props => props.theme.colors.primary};
  `};
`

const InnerCircle = styled(Animated.View)`
  width: ${DOT_SIZE - (DOT_BORDER_WIDTH * 2)}px;
  height: ${DOT_SIZE - (DOT_BORDER_WIDTH * 2)}px;
  border-radius: ${DOT_SIZE - (DOT_BORDER_WIDTH * 2) * .5}px;
  background-color: ${props => props.theme.colors.primary};
`
