// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../../components/screen'
import Button from '../../../components/button'
import { Body } from '../../../components/typography'
import { STORE_KEYS } from '../../../stores'

// Export component
const ConfirmCred = ({ navigation, username, password, pincode }) => {
  return (
    <Wrapper behavior="padding" enabled>
      <Screen title={'Account Confirmation'} {...{ navigation }}>
        <Container>
          <Top>
            <Description>
              Almost done!{"\n"} Let's write down your account information
            </Description>
            <Warning>
              If you lose your account information, you'll lose access to your funds permanently. Write down and store it securely.
            </Warning>
            <Credentials>
              Username: {username}{"\n"}
              Password: {password}{"\n"}
              PIN: {pincode}
            </Credentials>
          </Top>
          <Bottom>
            <Button label="Next" onPress={() => navigation.navigate('OnboardingConfirm2')} primary large style={{ width: '100%' }} />
          </Bottom>
        </Container>
      </Screen>
    </Wrapper>
  )
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: {
         username, password, pincode,
       }
     }) => ({
      username, password, pincode,
    })
  )
)(ConfirmCred);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
  text-align: center;
`

const Warning = styled(Body)`
  border: 1px solid #e00;
  padding: 10px;
  margin-top: 15px;
  text-align: center;
`

const Credentials = styled(Body)`
  padding: 10px;  
  margin-top: 10px;
  font-weight: bold;
  text-align: center;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`
