// Dependencies
import React from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../../../components/screen'
import {Heading, Body} from '../../../../components/typography'
import Button from '../../../../components/button'
import { STORE_KEYS } from '../../../../stores'

// Export Component
const AddToken = ({ navigation, init }) => {
  const onNextStep = () => {
    init()
    navigation.navigate('OnboardingStep2')
  }

  return (
    <Screen title="Add a Token" {...{ navigation }}>
      <Container>
        <ButtonWrapper>
          <Button label="Add" onPress={onNextStep} primary large/>
        </ButtonWrapper>
      </Container>
    </Screen>
  )
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { init },
     }) => ({
      init,
    })
  )
)(AddToken);

// Styles
const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  align-items: center;
`

const Title = styled(Heading)``

const SubTitle = styled(Body)`
  margin-top: auto;
  margin-bottom: 10px;
`

const ButtonWrapper = styled.View`
  width: 100%;
  height: 50px;
  margin-top: auto;
`
