// Dependencies
import React, { useState, Component } from 'react'
import { Platform } from 'react-native';
import styled from 'styled-components/native'
import * as Haptics from 'expo-haptics'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../components/screen'
import Button from '../../components/button'
import TextInputField from '../../components/text-input-field'
import { Body } from '../../components/typography'
import { STORE_KEYS } from '../../stores'
import Progress from '../../components/progress'

class TfaVerify extends Component {
  constructor(props) {
    super(props);

    this.state = {
      code: '',
      error: '',
      showProgress: false,
      done: false,
    }
  }

  onVerify2FA = () => {
    const { code } = this.state;
    const { verify2FA, navigation } = this.props;
    if ((code || '').trim().length > 0) {
      this.setState({
        error: '',
      });

      // Show progress
      this.setShowProgress(true);

      verify2FA(code)
        .then(success => {
          // console.log('success:', success);
          if (success) {
            setTimeout(() => {
              this.setDone(true)
              if (Platform.OS === 'ios' || Platform.OS === 'android') {
                setTimeout(() => Haptics.impactAsync(Haptics.ImpactFeedbackStyle.Heavy), 500)
              }
            }, 2500);

            // Navigate away
            setTimeout(() => {
              navigation.navigate('SettingsMain')
            }, 5000);
          } else {
            this.setShowProgress(false);
            this.setState({
              error: "2FA Code verification is failed."
            });
          }
        })
        .catch(err => {
          this.setShowProgress(false);
          this.setState({
            error: "2FA Code verification is failed."
          });
        });
    } else {
      this.setState({
        isValid: false,
        error: "Please input code correctly."
      });
    }
  };

  setShowProgress = (mode) => {
    this.setState({
      showProgress: mode,
    });
  };

  onChangeCode = (value) => {
    this.setState({
      code: value,
    });
  };

  setDone = () => {
    const { done: don } = this.state;
    this.setState({
      done: !don,
    });
  };

  render() {
    const { error, done, code, showProgress } = this.state
    const { navigation } = this.props

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Verify Code'} {...{ navigation }}>
          <Container>
            <Top>
              <Label>Code</Label>
              <TextInputField
                secureTextEntry={true}
                onChangeText={this.onChangeCode}
                value={code}
              />
              <Description>
                You can find 6 digits from Google Auth app. Please input here to complete 2FA process.
              </Description>
              {(error || '').trim().length > 0 && (
                <Error>{ error }</Error>
              )}
            </Top>
            <Bottom>
              <Button label="Verify" onPress={this.onVerify2FA} primary large style={{ width: '100%' }} />
            </Bottom>
          </Container>
        </Screen>
        {showProgress && <Progress done={done}/> }
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { verify2FA },
     }) => ({
      verify2FA,
    })
  )
)(TfaVerify);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
