// Dependencies
import React, { useState, useCallback, Fragment } from 'react'
import * as Haptics from 'expo-haptics'
import { Dimensions, Platform } from 'react-native'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'
import theme from '../../theme'

// Components
import Progress from '../../components/progress'
import Screen from '../../components/screen'
import ScrollView from '../../components/scroll-view'
import ConfirmSlider from '../../components/confirm-slider'
import { Body } from '../../components/typography'
import { STORE_KEYS } from '../../stores'
import { Item, Section } from '../../components/transaction-detail'
import CoinHeader from '../../components/coin-header'

// Get screen width
const { width } = Dimensions.get('screen')

// Prepare slider  with
const sliderWidth = width - (theme.spacing * 3)

// Export component
const confirmTx = ({ amountOfSend, coinOfSend, addressOfSend, sendCoin, navigation }) => {
  // Handle state
  const [done, setDone] = useState(false)
  const [showProgress, setShowProgress] = useState(false)
  const [error, setError] = useState('')
  const itemData = navigation.getParam('data', {});
  const code = itemData.code || '';
  let lockConfirm = false;

  // Handle confirmation
  const onConfirm = useCallback(() => {
    if (lockConfirm) return;
    lockConfirm = true;

    // Show progress
    setShowProgress(true)

    // console.log('[onConfirm]', amountOfSend, coinOfSend, addressOfSend);
    sendCoin(code)
      .then(() => {
        // Show 'done'
        setTimeout(() => {
          setDone(true)
          if (Platform.OS === 'ios' || Platform.OS === 'android') {
            setTimeout(() => Haptics.impactAsync(Haptics.ImpactFeedbackStyle.Heavy), 500)
          }
        }, 2500)

        // Navigate away
        setTimeout(() => {
          lockConfirm = false;
          navigation.navigate('Main')
        }, 5000)
      })
      .catch(err => {
        setShowProgress(false)
        setError(err)
        lockConfirm = false;
      })
  }, [])

  return (
    <Fragment>
      <Screen title={"Confirmation"} {...{ navigation }}>
        <Container>
          <ScrollView>
            <CoinHeader image={itemData.image} title={itemData.title} amount={itemData.description}/>
            <Section title={'Details'}>
              <Item label='Amount' value={`${amountOfSend} ${coinOfSend}`} />
              <Item label='Transaction Fee' value="1/10th a cent" />
              <Item label='Recipient Address' value={addressOfSend} collapsed />
            </Section>
            {(error || '').length > 0 && (
              <Error>{ error }</Error>
            )}
            <Bottom>
              <ConfirmSlider
                width={sliderWidth}
                label="Slide to Confirm"
                onConfirm={onConfirm}
              />
            </Bottom>
          </ScrollView>
        </Container>
      </Screen>
      {showProgress && <Progress done={done}/>}
    </Fragment>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         amountOfSend,
         coinOfSend,
         addressOfSend,
         sendCoin,
       }
     }) => ({
      amountOfSend,
      coinOfSend,
      addressOfSend,
      sendCoin,
    })
  )
)(confirmTx);

// Styles
const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px 0;
  justify-content: space-between;
`

const Credentials = styled(Body)`
  font-weight: bold;
  text-align: left;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding: ${props => props.theme.spacing}px;  
`;

const Bottom = styled.View`
  margin-top: auto;
  margin-bottom: ${props => props.theme.spacing * 1.5}px;
  align-items: center;
`
