// Dependencies
import React from 'react'
import styled from 'styled-components/native'
import { FontAwesome } from '@expo/vector-icons'

// Components
import { Body, Heading } from './typography'
import theme from '../theme'
import TouchableOpacity from './touchable-opacity'

// Profile image size
const imageSize = theme.touchTargetSize

// Export component
export default ({ image, title, description, coinAmount, usdAmount, navigation, item }) => (
  <Header>
    <HeaderWrapper>
      <HeaderContent>
        <HeaderImage
          source={image}
        />
      </HeaderContent>
      <Title center>
        {title}
      </Title>
      <Description center>
        {description}
      </Description>
    </HeaderWrapper>
    <ArrowContainer>
      <FontAwesome name="long-arrow-up" size={imageSize * .5} color={theme.colors.white} />
    </ArrowContainer>
    <CoinAmount center>
      {coinAmount}
    </CoinAmount>
    <UsdAmount center>
      {usdAmount}
    </UsdAmount>
  </Header>
)

// Styles
const Header = styled.View`
  margin-bottom: ${props => props.theme.spacing * 2}px;
  justify-content: center;
`

const HeaderWrapper = styled(TouchableOpacity)`
`

const HeaderContent = styled.View`
  margin: ${props => props.theme.spacing}px auto;
  justify-content: center;
  align-items: center;
`

const Title = styled(Heading)`
  font-size: 22px;
  line-height: 30px;
`

const Description = styled(Body)`
  font-size: 16px;
  line-height: 22px;
`

const HeaderImage = styled.Image`
  width: 126px;
  height: 126px;
  border-radius: 63px;
`

const ArrowContainer = styled.View`
  margin: ${props => props.theme.spacing}px auto;
`

const CoinAmount = styled(Title)`
  font-size: 36px;
  line-height: 48px;
`

const UsdAmount = styled(Body)`
  font-size: 16px;
  line-height: 22px;
`
