// Dependencies
import React, { useState, Component } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../components/screen'
import Button from '../../components/button'
import TextInputField from '../../components/text-input-field'
import { Body } from '../../components/typography'
import { STORE_KEYS } from '../../stores'
import Progress from '../../components/progress'

class TfaConfirm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      password: '',
      error: '',
    }
  }

  onEnable2FA = () => {
    const { password } = this.state;
    const { checkPass, enable2FA, navigation } = this.props;
    if ((password || '').trim().length > 0) {
      this.setState({
        error: '',
      });

      checkPass(password)
        .then(success => {
          // console.log('success:', success);
          if (success) {
            enable2FA()
              .then(data => {
                navigation.navigate('TfaSetup')
              })
          } else {
            this.setState({
              error: "Password verification is failed."
            })
          }
        })
        .catch(err => {
          this.setState({
            error: "Password verification is failed."
          })
        })
    } else {
      this.setState({
        isValid: false,
        error: "Please input password correctly."
      })
    }
  };

  onChangePass = (value) => {
    this.setState({
      password: value,
    });
  };

  render() {
    const { error, password } = this.state
    const { navigation, loading } = this.props

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Two Factor'} {...{ navigation }}>
          <Container>
            <Top>
              <Label>Password</Label>
              <TextInputField
                secureTextEntry={true}
                onChangeText={this.onChangePass}
                value={password}
              />
              <Description>
                Enable 2-Factor Authentication to increase security by using google authenticator app.
                Two-step verification provides an additional level of security and makes your account more protected.
              </Description>
              {(error || '').trim().length > 0 && (
                <Error>{ error }</Error>
              )}
            </Top>
            <Bottom>
              <Button label="Enable" onPress={this.onEnable2FA} primary large style={{ width: '100%' }} />
            </Bottom>
          </Container>
        </Screen>
        { loading && <Progress done={false}/> }
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { checkPass, enable2FA, loading },
     }) => ({
      checkPass,
      enable2FA,
      loading,
    })
  )
)(TfaConfirm);


// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
