// Dependencies
import React, { Fragment, useState, useEffect } from 'react'
import styled from 'styled-components/native'
import { SplashScreen } from 'expo'
import theme from '../../theme'

// Components
import Screen from '../../components/screen'
import Header from '../../components/logo'
import { Body, Heading } from '../../components/typography'
import SlideUp from '../../components/animations/slide-up'
import FadeIn from '../../components/animations/fade-in'
import Button from '../../components/button'

// Export component
export default ({ navigation }) => {
  const [ready, setReady] = useState(false)

  // Hide the splashscreen
  useEffect(() => {
    SplashScreen.hide();
    setReady(true);
  }, [])

  return (
    <Screen>
      <Container>
        {ready && <Fragment>
          <Header delay={200}/>
          <FadeIn delay={200} duration={1000}>
            <Content>
              <Body style={{ marginTop: theme.spacing * .5 }} center>
                The secure and easy to use mobile wallet for PegNet assets
              </Body>
            </Content>
          </FadeIn>
          <Bottom>
            <SlideUp delay={1000}>
              <Button label="Create account" onPress={() => navigation.navigate('OnboardingStep1')} primary large/>
              <Button label="Already have an account? Sign in" onPress={() => navigation.navigate('OnboardingSign')} style={{ marginTop: 5 }} small link/>
            </SlideUp>
          </Bottom>
        </Fragment>}
      </Container>
    </Screen>
  )
}

// Styles
const Container = styled.View`
  flex: 1;
  flex-direction: column;
`

const Content = styled.View`
  margin-top: ${props => props.theme.spacing}px;
  padding: ${props => props.theme.spacing * 2}px;
`

const Bottom = styled.View`
  margin-top: auto;
  padding: ${props => props.theme.spacing}px ${props => props.theme.spacing * 2}px;
`
