// Dependencies
import React from 'react';
import {createAppContainer, createStackNavigator, createSwitchNavigator, createBottomTabNavigator} from 'react-navigation'

// Screens
import Loading from './screens/loading'
import OnboardingMain from './screens/onboarding'
import OnboardingPinCode from './screens/onboarding/pin-code'
import OnboardingLogin from './screens/onboarding/pin-login'
import SettingsMain from './screens/settings'
import SettingsEdit from './screens/settings/edit'
import SendMain from './screens/send'
import SendRecipient from './screens/send/recipient'
import VerifySend from './screens/send/verifySend'
import CoinDash from './screens/coindash'
import SendConfirm from './screens/send/confirm'
import QRScan from './screens/send/qrscan'
import ReceiptMain from './screens/receipt'
import ProfileMain from './screens/profile'
import Transactions from './screens/transactions'
import ExAmountConfirm from './screens/home/exchange/amount'
import TfaConfirm from './screens/settings/tfaConfirm'
import TfaSetup from './screens/settings/tfaSetup'
import TfaVerify from './screens/settings/tfaVerify'
import TfaLogin from './screens/onboarding/signin/tfaLogin'
import ChangePass from './screens/settings/changePass'
import ChangePin from './screens/settings/changePin'
import BackupWallet from './screens/settings/backupWallet'

import SignUpStep1 from './screens/onboarding/signup/step1'
import SignUpStep2 from './screens/onboarding/signup/step2'
import SignUpStep3 from './screens/onboarding/signup/step3'
import ConfirmStep1 from './screens/onboarding/signup/confirm1'
import ConfirmStep2 from './screens/onboarding/signup/confirm2'
import SignIn from './screens/onboarding/signin'

import Wallets from './screens/home/wallets'
import AddWallet from './screens/home/wallets/AddWallet'
import AddToken from './screens/home/wallets/AddToken'
import Buy from './screens/home/buy'
import Sell from './screens/home/sell'
import Exchange from './screens/home/exchange'
import VerifyExchange from './screens/home/exchange/verifyExchange';
import TabBarIcon from "./components/TabBarIcon";

// Shared config
const config = {
  headerMode: 'none',
  cardStyle: {backgroundColor: 'rgba(255,0,0,0)'}
}

// Send stack
const SendStack = createStackNavigator(
  {
    SendMain: {
      screen: SendMain
    },
    SendConfirm: {
      screen: SendConfirm
    },
    QRScan: {
      screen: QRScan
    },
    SendRecipient: {
      screen: SendRecipient
    },
    VerifySend: {
      screen: VerifySend
    }
  },
  {
    ...config,
    initialRouteName: 'SendMain',
    mode: 'modal'
  }
)

// Receipt stack
const ReceiptStack = createStackNavigator(
  {
    ReceiptMain: {
      screen: ReceiptMain
    },
    Profile: {
      screen: ProfileMain
    }
  },
  {
    ...config,
    initialRouteName: 'ReceiptMain'
  }
)

// CoinDash stack
const CoinDashStack = createStackNavigator(
  {
    CoinDashMain: {
      screen: CoinDash
    }
  },
  {
    ...config,
    initialRouteName: 'CoinDashMain'
  }
)

// Settings stack
const SettingsStack = createStackNavigator(
  {
    SettingsMain: {
      screen: SettingsMain
    },
    SettingsEdit: {
      screen: SettingsEdit
    }
  },
  {
    ...config,
    initialRouteName: 'SettingsMain'
  }
)

// Onboarding stack
const OnboardingStack = createStackNavigator(
  {
    OnboardingMain: {
      screen: OnboardingMain
    },
    OnboardingPinCode: {
      screen: OnboardingPinCode
    },
    OnboardingLogin: {
      screen: OnboardingLogin
    },
    OnboardingStep1: {
      screen: SignUpStep1
    },
    OnboardingStep2: {
      screen: SignUpStep2
    },
    OnboardingStep3: {
      screen: SignUpStep3
    },
    OnboardingConfirm1: {
      screen: ConfirmStep1
    },
    OnboardingConfirm2: {
      screen: ConfirmStep2
    },
    OnboardingSign: {
      screen: SignIn
    },
    TfaLogin: {
      screen: TfaLogin
    }
  },
  {
    ...config,
    initialRouteName: 'OnboardingMain'
  }
)


// App stack
const MainNavStack = createBottomTabNavigator(
  {
    Wallets: {
      screen: Wallets,
      navigationOptions: {
        tabBarIcon: ({ focused }) => <TabBarIcon focused={focused} name="md-wallet" />,
      }
    },
    Buy: {
      screen: Buy,
      navigationOptions: {
        tabBarIcon: ({ focused }) => <TabBarIcon focused={focused} name="md-basket" />,
      }
    },
    Sell: {
      screen: Sell,
      navigationOptions: {
        tabBarIcon: ({ focused }) => <TabBarIcon focused={focused} name="md-send" />,
      }
    },
    Exchange: {
      screen: Exchange,
      navigationOptions: {
        tabBarIcon: ({ focused }) => <TabBarIcon focused={focused} name="md-book" />,
      }
    },
  },
  {
    initialRouteName: "Wallets",
    tabBarOptions: {
      style: {
        height: 55,
        backgroundColor: '#151a1d',
        borderTopWidth: 0.2,
        borderTopColor: '#1f242e'
      },
      activeTintColor: '#2b72b5',
    }
  }
)

// App stack
const AppStack = createStackNavigator(
  {
    Main: {
      screen: MainNavStack
    },
    Settings: {
      screen: SettingsStack
    },
    Send: {
      screen: SendStack
    },
    Receipt: {
      screen: ReceiptStack
    },
    CoinDash: {
      screen: CoinDashStack
    },
    AddWallet: {
      screen: AddWallet
    },
    AddToken: {
      screen: AddToken
    },
    Transactions: {
      screen: Transactions
    },
    ExAmountConfirm: {
      screen: ExAmountConfirm
    },
    VerifyExchange: {
      screen: VerifyExchange,
    },
    TfaConfirm: {
      screen: TfaConfirm
    },
    ChangePass: {
      screen: ChangePass
    },
    ChangePin: {
      screen: ChangePin
    },
    BackupWallet: {
      screen: BackupWallet
    },
    TfaSetup: {
      screen: TfaSetup
    },
    TfaVerify: {
      screen: TfaVerify
    }
  },
  {
    ...config,
    initialRouteName: 'Main',
    mode: 'modal'
  }
)

// Auth stack
const AuthStack = createSwitchNavigator(
  {
    Loading: Loading,
    App: AppStack,
    Onboarding: OnboardingStack
  },
  {
    initialRouteName: 'Loading'
  }
)

export default createAppContainer(AuthStack)
