// Dependencies
import React from 'react'
import {Image, View} from 'react-native'
import styled, { css } from 'styled-components/native'
import theme from '../theme'

// Components
import TouchableOpacity from './touchable-opacity'
import Copper from '../assets/images/copper.png'
import Silver from '../assets/images/silver.png'
import Gold from '../assets/images/gold.png'
import Platinum from '../assets/images/platinum.png'
import Diamond from '../assets/images/diamond.png'
import Staking from '../assets/images/staking.png'
import Lottery from '../assets/images/lottery-transition-item.png'
import TransactionReceive from '../assets/icons/transaction-receive-icon.png'
import TransactionSend from '../assets/icons/transaction-send-icon.png'
import LinearGradient from './gradient'

// Prepare gradient
const Gradient = ({ children, primary, ...props }) => {
  return primary ? (
    <LinearGradient {...props}>
      {children}
    </LinearGradient>
  ) : (
    <View {...props}>
      {children}
    </View>
  )
}

// Export component
export default ({ title, description, coinAmount = 0, usdAmount = 0, image, onPress, type }) => {
  let sourceImage = image;
  if (type) {
    switch(type) {
      case 'TransactionReceive':
        sourceImage = TransactionReceive
        break;
      case 'TransactionSend':
        sourceImage = TransactionSend
        break;
      case 'Copper':
        sourceImage = Copper
        break
      case 'Silver':
        sourceImage = Silver
        break
      case 'Gold':
        sourceImage = Gold
        break
      case 'Platinum':
        sourceImage = Platinum
        break
      case 'Diamond':
        sourceImage = Diamond
        break
      case 'Staking':
        sourceImage = Staking
        break;
      case 'Lottery':
        sourceImage = Lottery
        break;
    }
  }

  const primary = type === 'TransactionReceive'

  // Profile image size
  const imageSize = type === 'TransactionReceive' || type === 'TransactionSend' ? 18 : theme.itemSize

  return (
    <Container
      as={onPress ? TouchableOpacity : null}
      onPress={() => onPress({ image: sourceImage })}
    >
      <ImageRounded type={type} primary={primary}>
        <Image
          source={sourceImage}
          style={{ width: imageSize, height: imageSize }}
        />
      </ImageRounded>
      <FromContainer>
        <TextField>
          {title}
        </TextField>
        <TextField subtitle>{description}</TextField>
      </FromContainer>
      <AmountContainer>
        <TextField right>{coinAmount}</TextField>
        <TextField subtitle>{usdAmount}</TextField>
      </AmountContainer>
    </Container>
  )
}

// Styles
const Container = styled.View`
  height: ${props => props.theme.itemSize}px;
  flex-direction: row;
`

const ImageRounded = styled(Gradient)`
  width: ${props => props.theme.itemSize}px;
  height: ${props => props.theme.itemSize}px;
  border-radius: ${props => props.theme.itemSize * .5}px;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  
  ${props => props.type === 'TransactionSend' && css`
    background-color: ${props => props.theme.colors.tertiary};
  `}
`

const FromContainer = styled.View`
  flex: 1;
  padding: 0 ${props => props.theme.spacing}px;
  justify-content: center;
`

const AmountContainer = styled.View`
  justify-content: center;
  align-items: flex-end;
`

const TextField = styled.Text`
  font-family: ${props => props.theme.fonts.regular};
  color: ${props => props.theme.colors.white};
  font-size: 16px;
  line-height: 22px;
  
  ${props => props.right && css`
    font-size: 17px;
    line-height: 23px;
  `};

  ${props => props.subtitle && css`
    margin-top: 2px;
    color: ${props => props.theme.colors.light};
    font-size: 14px;
    line-height: 19px;
  `};
`
