// Dependencies
import React, { Fragment, useState, useEffect, useCallback } from 'react'
import styled from 'styled-components/native'
import theme from '../../theme'

// Components
import Screen from '../../components/screen'
import { Heading, Body } from '../../components/typography'
import PinCodeDots from '../../components/pin-code-dots'
import NumberPad from '../../components/number-pad'
import SlideUp from '../../components/animations/slide-up'
import FadeIn from '../../components/animations/scale-in'
import BottomSheet from '../../components/bottom-sheet'
import ForgotPasswordSheet from '../../components/forgot-password'

// Constants
const PIN_LENGHT = 4

// Prepare performance sheet
const CurrencySheetMemo = React.memo(({ active, onClose }) => {
  return (
    <BottomSheet active={active} onClose={onClose}>
      <ForgotPasswordSheet onCancel={onClose}/>
    </BottomSheet>
  )
})

// Export component
export default ({ navigation }) => {
  const [ready, setReady] = useState(false)
  const [showCurrencies, setShowCurrencies] = useState(false)
  const [pin, setPin] = useState('')

  const onCurrencySheetClose = useCallback(() => {
    setShowCurrencies(false)
  }, [])

  useEffect(() => {
    setReady(true)
  }, [])

  // useEffect(() => {
  //   pin.length === PIN_LENGHT && setTimeout(() => {
  //     navigation.navigate('App')
  //   }, 200)
  // }, [pin])

  const numberPadHandler = (type, value) => {
    switch(type) {
      case 'number':
        pin.length < PIN_LENGHT && setPin(pin => pin + value)
        break;
      case 'remove':
        setPin(pin => pin.slice(0, -1))
        break;
      case 'next':
        // navigation.navigate('App')
        break;
    }
  }

  return (
    <Fragment>
      <Screen
        // right={{ icon: 'detail', onPress: () => setShowCurrencies(true) }}
        {...{ navigation }}
      >
        <Container>
          <Avatar source={require('../../assets/profile-image.png')} />
          <Title center>Input 4 digits pin codes</Title>
          {ready && (
            <Fragment>
              <FadeIn>
                <PinCodeDots
                  style={{ marginTop: theme.spacing * 2 }}
                  length={PIN_LENGHT}
                  value={pin}
                />
              </FadeIn>
              <Bottom>
                <SlideUp>
                  <NumberPad
                    onPress={numberPadHandler}
                    button="Touch"
                  />
                </SlideUp>
              </Bottom>
            </Fragment>
          )}
        </Container>
      </Screen>
      <CurrencySheetMemo active={showCurrencies} onClose={onCurrencySheetClose}/>
    </Fragment>
  )
}

// Styles
const Container = styled.View`
  flex: 1;
  align-items: center;
`

const Bottom = styled.View`
  margin-top: auto;
  width: 100%;
`

const Title = styled(Heading)`
  font-size: 16px;
  line-height: 22px;
`

const SubTitle = styled(Body)`
  font-size: 16px;
`

const Avatar = styled.Image`
  width: 80px;
  height: 80px;
  border-radius: 40px;
  margin-bottom: ${props => props.theme.spacing * 0.75}px;
`
