// Dependencies
import React from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../../components/screen'
import {Heading, Body} from '../../../components/typography'
import Button from '../../../components/button'
import { STORE_KEYS } from '../../../stores'

// Export Component
const Step1 = ({ navigation, init }) => {
  const onNextStep = () => {
    init()
    navigation.navigate('OnboardingStep2')
  }

  return (
    <Screen title="Step 1/3" {...{ navigation }}>
      <Container>
        <SubTitle>Let's get started by creating your account login. You'll choose a username and password, which we'll use
          to encrypt your account. Not even PegNet has access to your information, so you have full and complete control
          over your digital assets.</SubTitle>
        <SubTitle>Let's get started with choosing a username</SubTitle>
        <ButtonWrapper>
          <Button label="Get Started" onPress={onNextStep} primary large/>
        </ButtonWrapper>
      </Container>
    </Screen>
  )
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { init },
     }) => ({
      init,
    })
  )
)(Step1);

// Styles
const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  align-items: center;
`

const Title = styled(Heading)``

const SubTitle = styled(Body)`
  margin-top: auto;
  margin-bottom: 10px;
`

const ButtonWrapper = styled.View`
  width: 100%;
  height: 50px;
`
