// Dependencies
import React from 'react'
import styled, { css } from 'styled-components/native'

// Components
import { Body } from './typography'

// Export section component
export const Section = ({ children, title }) => {
  return (
    <SectionContainer>
      {!!title && (
        <SectionTitle>{title}</SectionTitle>
      )}
      <SectionContent>
        {React.Children.map(children, (child, index) => {
          const showSeperator = children.length > 1 && index !== children.length - 1

          return [
            child,
            showSeperator && <Seperator/>
          ]
        })}
      </SectionContent>
    </SectionContainer>
  )
}

// Export item component
export const Item = ({ label, value, collapsed, onPress }) => {
  return (
    <ItemContainer>
      <ItemLabel>{label}</ItemLabel>
      <ItemValue
        collapsed={collapsed}
        spaceAfter={!!onPress}
        ellipsizeMode="middle"
        numberOfLines={1}
        onPress={() => {
          !!onPress && onPress();
        }}
      >{value}</ItemValue>
    </ItemContainer>
  )
}

// Styles
const SectionContainer = styled.View`
  margin: 0 ${props => props.theme.spacing}px;
`

const SectionContent = styled.View`
  background-color: ${props => props.theme.colors.tertiary};
  border-radius: 5px;
  padding: ${props => props.theme.spacing * .5}px ${props => props.theme.spacing}px;
`

const SectionTitle = styled(Body)`
  text-transform: uppercase;
  font-size: 12px;
  margin-bottom: ${props => props.theme.spacing * .5}px;
`

const Seperator = styled.View`
  height: 1px;
`

const ItemContainer = styled.View`
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  height: 36px;
`

const ItemLabel = styled(Body)`
  line-height: 36px;
  font-size: 14px;
`

const ItemValue = styled(Body)`
  font-family: ${props => props.theme.fonts.bold};
  color: ${props => props.theme.colors.white};
  line-height: 36px;

  ${props => props.collapsed && css`
    max-width: 135px;
  `}

  ${props => props.spaceAfter && css`
    color: ${props => props.theme.colors.link};
  `};
`
