// Dependencies
import React from 'react'
import styled from 'styled-components/native'
import { FlatList } from 'react-native'
import Animated from 'react-native-reanimated'
import { onScroll } from 'react-native-redash'

// Components
import Section from '../transaction-section'

// Prepare animated FlatList
const AnimatedFlatList = Animated.createAnimatedComponent(FlatList)

// Bottom padding to account for fixed footer
const paddingBottom = 54

// Export component
export default ({ y, data, navigation }) => {
  return (
    <Container
      onScroll={onScroll({ y })}
      showsVerticalScrollIndicator={false}
      overScrollMode="never"
      initialNumToRender={50}
      scrollEventThrottle={16}
      contentContainerStyle={{ flexGrow: 1, paddingBottom }}
      keyExtractor={(item, index) => `section-${index}`}
      data={data}
      CellRendererComponent={({children}) => (
        <SectionDivider pointerEvents="box-none">{children}</SectionDivider>
      )}
      renderItem={({ item }) => (
        <Section {...item} {...{ navigation }} />
      )}
    />
  )
}

const Container = styled(AnimatedFlatList)`
  flex: 1;
`

const SectionDivider = styled.View`
  margin-bottom: ${props => props.theme.spacing * 1.5}px;
  position: relative;
  z-index: 100;
`
