// Dependencies
import React, { useEffect, useState, useRef, Fragment, useCallback } from 'react'
import { SplashScreen } from 'expo'
import { Image } from 'react-native'
import Animated from 'react-native-reanimated'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../../components/screen'
import MainTransactions from '../../../components/main-transactions'
import Footer from '../../../components/footer'
import FadeIn from '../../../components/animations/fade-in'
import { STORE_KEYS } from '../../../stores'
import { commafyDigitFormat } from '../../../utils';

// Images
import TopupIcon from '../../../assets/icons/topup-icon.png'

// Dummy data
import data from './data'

// Prepare animated
const { Value } = Animated

// Prepare wallets creen
const Main = React.memo(({ navigation, myBalances, getCoinPrice, priceUpdatedAt }) => {
  // Ready state
  const [ready, setReady] = useState(false)

  // Animated scroll value
  const [y] = useState(new Value(0))

  // Hide the splashscreen
  useEffect(() => {
    SplashScreen.hide()
    setReady(true)
  }, [])

  // Main Footer Buttons
  const footerButtons = [
    {
      // onPress: () => navigation.navigate('AddWallet'),
      before: <Image source={TopupIcon} style={{ width: 15, height: 14, marginTop: 2 }}/>,
      label: 'Add Wallet'
    }, {
      // onPress: () => navigation.navigate('AddToken'),
      before: <Image source={TopupIcon} style={{ width: 15, height: 14, marginTop: 2 }}/>,
      label: 'Add Token',
      primary: true
    }
  ]

  const right = {
    icon: 'menu',
    onPress: () => navigation.navigate('Settings')
  }

  for (let i = 0; i < data[0].items.length; i++) {
    const item = data[0].items[i];
    const balance = myBalances[item.key] || 0;
    const price = getCoinPrice(item.key);
    data[0].items[i].coinAmount = `${balance} ${item.key}`;
    data[0].items[i].balance = balance;
    data[0].items[i].usdAmount = commafyDigitFormat(balance * price, 3) + ' USD';
    data[0].items[i].coinUSDPrice = price;
  }

  return (
    <Screen title="My Wallets" {...{ navigation, right }} left={false}>
      {ready && (
        <FadeIn style={{ flex: 1 }}>
          {/* Scrollable transactions (Flatlist) */}
          <MainTransactions
            // Pass props to flatlist
            {...{ y, navigation, data }}
          />

          {/* Buttons */}
          <Footer buttons={footerButtons} />
        </FadeIn>
      )}
    </Screen>
  )
})

// Export component
const Wallet = ({ getCoinPrice, myBalances, priceUpdatedAt, navigation }) => {
  return (
    <Fragment>
      <Main {...{navigation, myBalances, getCoinPrice, priceUpdatedAt}} />
    </Fragment>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE, STORE_KEYS.COINPRICESTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         myBalances,
         priceUpdatedAt,
       },
       [STORE_KEYS.COINPRICESTORE]: {
         getCoinPrice,
       }
     }) => ({
      myBalances,
      priceUpdatedAt,
      getCoinPrice,
    })
  )
)(Wallet);
