// Dependencies
import React, { useState, Component } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../../components/screen'
import Button from '../../../components/button'
import TextInputField from '../../../components/text-input-field'
import { Body } from '../../../components/typography'
import { STORE_KEYS } from '../../../stores'
import Progress from '../../../components/progress'

class Step2 extends Component {
  constructor(props) {
    super(props);

    this.state = {
      error: '',
      isValid: true,
    }
  }

  onNextStep = () => {
    const { navigation, username, checkUsername } = this.props
    if ((username || '').trim().length > 0) {
      this.setState({
        isValid: true,
        error: '',
      })
      checkUsername(username)
        .then(success => {
          if (success) {
            navigation.navigate('OnboardingStep3')
          } else {
            this.setState({
              isValid: false,
              error: "Username already exists. Choose another one."
            })
          }
        })
    } else {
      this.setState({
        isValid: false,
        error: "Please input username correctly."
      })
    }
  }

  render() {
    const { error, isValid } = this.state
    const { navigation, username, setUsername, loading } = this.props

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Step 2/3'} {...{ navigation }}>
          <Container>
            <Top>
              <Label>Username</Label>
              <TextInputField
                onChangeText={setUsername}
                value={username}
              />
              {!isValid && (
                <Error>{ error }</Error>
              )}
              <Description>
                Your username will be required to sign in to your PegNet account on this and other devices.
              </Description>
            </Top>
            <Bottom>
              <Button label="Next" onPress={this.onNextStep} primary large style={{ width: '100%' }} />
            </Bottom>
          </Container>
        </Screen>
        { loading && <Progress done={false}/> }
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { username, setUsername, checkUsername, loading },
     }) => ({
      username,
      setUsername,
      checkUsername,
      loading,
    })
  )
)(Step2);


// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
