// Dependencies
import React from 'react'
import styled from 'styled-components/native'
import Item from './transaction-item'

// Export component
export default ({ items, navigation, onPress, ...props }) => {
  return (
    <Container {...props}>
      <Items>
        {items.map((item, index) =>
          <ItemsWrapper key={`item-${index}`}>
            <Item
              onPress={({ image }) => navigation.navigate('CoinDash', { transaction: { ...item, image } })}
              {...item}
            />
          </ItemsWrapper>
        )}
      </Items>
    </Container>
  )
}

// Styles
const Container = styled.View`
  padding: 0 ${props => props.theme.spacing}px;
`

const Title = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  text-transform: uppercase;
`

const Items = styled.View`
`

const ItemsWrapper = styled.View`
  margin-top: ${props => props.theme.spacing * .8}px;
`
