// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'
import { Platform } from 'react-native';
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../components/screen'
import Footer from '../../components/footer'
import TextInputField from "../../components/text-input-field";
import { Body } from "../../components/typography";
import { STORE_KEYS } from '../../stores'

// Export component
const ReceiptComp = ({ addressOfSend, setAddressOfSend, navigation }) => {
  const itemData = navigation.getParam('data', {});
  const [isValid, setIsValid] = useState(true)
  const [error, setError] = useState('')

  const setAddress = (value) => {
    setAddressOfSend(value || '');
  };

  const footerButtons = [
    {
      onPress: () => {
        if (Platform.OS === 'ios' || Platform.OS === 'android') {
          navigation.navigate('QRScan');
        }
      },
      label: 'Scan QR'
    }, {
      onPress: () => {
        if ((addressOfSend || '').trim().length === 0) {
          setError('Please input receiving address')
          setIsValid(false);
        } else {
          setIsValid(true);
          navigation.navigate('VerifySend', { data: { ...itemData } })
        }
      },
      label: 'Next',
      primary: true
    }
  ]

  return (
    <Wrapper behavior="padding" enabled>
      <Screen title={'Enter address'} {...{ navigation }}>
        <Container>
          <Top>
            <Label>Receiving Address</Label>
            <TextInputField
              onChangeText={setAddress}
              value={addressOfSend}
            />
            {!isValid && (
              <Error>{ error }</Error>
            )}
            <Description>
              A receiving address is required to send your PegNet assets. You can input the address manually or use your camera to scan the QR code.
            </Description>
          </Top>
        </Container>
        <Footer buttons={footerButtons} />
      </Screen>
    </Wrapper>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         addressOfSend,
         setAddressOfSend,
       }
     }) => ({
      addressOfSend,
      setAddressOfSend,
    })
  )
)(ReceiptComp);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px ${props => props.theme.spacing}px 0;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
