// Dependencies
import React, { useEffect, useState } from 'react'
import styled from 'styled-components/native'

// Components
import BottomSheet from '../../components/bottom-sheet'
import Screen from '../../components/screen'
import FadeIn from '../../components/animations/fade-in'
import ProfileHeader from '../../components/profile-header'
import ScrollView from '../../components/scroll-view'
import TransactionSection from '../../components/transaction-section'

// Dummy data
import data from './data'

// Export component
export default ({ navigation }) => {
  // Ready state
  const [ready, setReady] = useState(false)
  const [show, setShow] = useState(false)

  // Hide the splashscreen
  useEffect(() => {
    setReady(true)
    setTimeout(() => setShow(true), 1000)
  }, [])

  const item = navigation.getParam('transaction', {
    title: '',
    description: '',
    image: require('../../assets/profile-image.png')
  });

  return (
    <Screen title="Profile" {...{ navigation }}>
      {ready && (
        <FadeIn style={{ flex: 1 }}>
          <ScrollView>
            <Container>
              <ProfileHeader {...{ navigation, ...item }} />
              <TransactionSection {...{navigation}} {...data} />
            </Container>
          </ScrollView>
        </FadeIn>
      )}
    </Screen>
  )
}

// Styles
const Container = styled.View`
  margin-bottom: ${props => props.theme.spacing}px;
`
