// Dependencies
import React, { useState, useRef } from 'react'
import { TouchableWithoutFeedback, Image, Platform } from 'react-native'
import Animated, { Easing } from 'react-native-reanimated'
import * as Haptics from 'expo-haptics'
import styled, { css } from 'styled-components/native'
import { timing } from 'react-native-redash'

// Prepare animated
const { Value, Clock, useCode, block, set, cond, greaterThan, stopClock } = Animated

// Components
import Button from './button'

// Constants
const HEIGHT = 300

// Handle col
const ColWrapper = ({ children, onPress, ...props }) => {
  const [active] = useState(new Value(0))
  const [opacity] = useState(new Value(0))
  const [clock] = useState(new Clock())

  useCode(
    () => block([
      cond(
        active,
        [
          set(active, 0),
          set(opacity, 1),
          stopClock(clock)
        ]
      ),
      cond(
        greaterThan(opacity, 0),
        set(
          opacity,
          timing({
            clock,
            duration: 500,
            easing: Easing.inOut(Easing.ease),
            from: opacity,
            to: 0
          })
        )
      )
    ]),
    []
  )

  return (
    <TouchableWithoutFeedback
      onPressIn={() => {
        active.setValue(1)
        onPress && onPress()
      }}
      {...props}>
      <Col>
        <ColActive style={{ opacity }}/>
        {children}
      </Col>
    </TouchableWithoutFeedback>
  )
}

// Memoized number pad
const NumberPad = React.memo(({ handleOnPress, type, button, ...props }) => {
  return (
    <Container {...props}>
      {[...new Array(4)].map((_, rowIndex) => (
        <Row key={`row-${rowIndex}`}>
          {[...new Array(3)].map((_, colIndex) => {
            // Calculate index (0-11)
            const index = (rowIndex * 3) + colIndex

            // Lower left corner
            if (index === 9) {
              if (type === 'decimal') {
                return (
                  <ColWrapper key={`col-${index}`} onPress={() => { handleOnPress('separator') }}>
                    <NumberText separator>.</NumberText>
                  </ColWrapper>
                )
              } else if (type === 'integer') {
                return (
                  <ColWrapper key={`col-${index}`} onPress={() => { handleOnPress('remove') }}>
                    <Image source={require('../assets/icons/remove-icon.png')} style={{ width: 30, height: 19 }}/>
                  </ColWrapper>
                )
              }
            }

            // Number 0
            if (index === 10) {
              return (
                <ColWrapper key={`col-${index}`} onPress={() => { handleOnPress('number', 0) }}>
                  <NumberText>0</NumberText>
                </ColWrapper>
              )
            }

            // Next button
            if (index === 11) {
              if (button === 'Back') {
                return (
                  <ColWrapper key={`col-${index}`} onPress={() => { handleOnPress('remove') }}>
                    <Image source={require('../assets/icons/remove-icon.png')} style={{ width: 30, height: 19 }}/>
                  </ColWrapper>
                )
              }
              if (button === 'Touch') {
                return (
                  <ColWrapper key={`col-${index}`} onPress={() => { handleOnPress('next') }}>
                    <Image source={require('../assets/icons/touch-icon.png')} style={{ width: 22, height: 27 }}/>
                  </ColWrapper>
                )
              }
              return (
                <Col key={`col-${index}`}>
                  <NextButton onPress={() => { handleOnPress('next') }} label={button} primary/>
                </Col>
              )
            }

            // Numbers 1-9
            return (
              <ColWrapper key={`col-${index}`} onPress={() => { handleOnPress('number', index + 1) }}>
                <NumberText>{index + 1}</NumberText>
              </ColWrapper>
            )
          })}
        </Row>
      ))}
    </Container>
  )
})

// Export component
export default ({ onPress, type = "integer", button = "Next", ...props }) => {
  const handleOnPress = (type, value) => {
    if (Platform.OS === 'ios' || Platform.OS === 'android') {
      Haptics.impactAsync(Haptics.ImpactFeedbackStyle.Medium)
    }
    onPress && onPress(type, value)
  }

  return (
    <NumberPad
      {...{ handleOnPress, type, button, ...props }}
    />
  )
}

// Styles
const Container = styled.View`
  width: 100%;
  height: ${HEIGHT}px;
`

const Row = styled.View`
  flex: 1 1 0;
  flex-direction: row;
  borderTopWidth: 1px;
  borderTopColor: #1f242e;
`

const Col = styled.View`
  flex: 1 1 0;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  borderRightWidth: 1px;
  borderRightColor: #1f242e;
`

const ColActive = styled(Animated.View)`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.2);
`

const NextButton = styled(Button)`
  margin: 0 ${props => props.theme.spacing}px;
`

const NumberText = styled.Text`
  color: ${props => props.theme.colors.white};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 22px;

  ${props => props.separator && css`
    font-size: 24px;
    margin-bottom: 12px;
  `};
`
