// Dependencies
import React, { useState } from 'react'
import { Image } from 'react-native'
import styled, { css } from 'styled-components/native'
import Animated, { Easing } from 'react-native-reanimated'
import { FontAwesome } from '@expo/vector-icons'
import { loop } from 'react-native-redash'
import theme from '../theme'

// Components
import TouchableOpacity from './touchable-opacity'

// Prepare animated
const { Value, useCode, set, cond, greaterThan } = Animated

// Cursor component
const Cursor = ({ amount }) => {
  // Prepare animation value
  const [progress] = useState(new Value(0))

  // Start animation loop
  useCode(
    () => set(progress, loop({
      duration: 1000,
      easing: Easing.linear
    }))
  , [amount])

  return (
    <TextCursor
      style={{
        opacity: cond(greaterThan(progress, .5), 0, 1)
      }}
    />
  )
}

// Export component
export default ({ inactive, amount, onDelete, unit }) => {
  // Prepare scale value
  let scale = 1

  // Scale amount container according to amount length
  if (amount.length >= 9) {
    scale = .8
  } else if (amount.length >= 6) {
    scale = .9
  }

  return (
    <Container>
      <AmountContainer
        style={{
          transform: [{ scale }]
        }}
      >
        <Amount {...{ inactive }}>{amount}</Amount>
        <Cursor {...{ amount }}/>
        <AmountUnitWrapper>
          <AmountUnit>{unit}</AmountUnit>
        </AmountUnitWrapper>
      </AmountContainer>
      {!inactive && (
        <ActionContainer onPressIn={onDelete}>
          <Image
            source={require('../assets/icons/remove-icon.png')}
            style={{ width: 30, height: 19 }}
          />
        </ActionContainer>
      )}
    </Container>
  )
}

// Styles
const Container = styled.View`
  height: 40px;
`

const AmountContainer = styled.View`
  margin: 0 70px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
`

const Amount = styled.Text`
  height: 40px;
  color: ${props => props.theme.colors.white};
  font-family: ${props => props.theme.fonts.regular};
  font-size: 38px;
  line-height: 45px;

  ${props => props.inactive && css`
    color: ${props => props.theme.colors.dim};
  `};
`

const AmountUnit = styled(Amount)`
  margin-right: ${props => props.theme.spacing * .5}px;
`

const AmountUnitWrapper = styled(TouchableOpacity)`
  margin-left: ${props => props.theme.spacing * .5}px;
  flex-direction: row;
  align-items: center;
`

const ActionContainer = styled(TouchableOpacity)`
  position: absolute;
  z-index: 100;
  top: 0;
  right: 20px;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: flex-end;
`

const TextCursor = styled(Animated.View)`
  background-color: ${props => props.theme.colors.primary};
  width: 2px;
  height: 40px;
`
