// Dependencies
import React, { useState, Component } from 'react'
import { Clipboard } from "react-native";
import QRCode from 'react-native-qrcode-svg'
import styled from 'styled-components/native'
import { compose, withProps } from "recompose"
import { inject, observer } from "mobx-react"

// Components
import Screen from '../../components/screen'
import Button from '../../components/button'
import TextInputField from '../../components/text-input-field'
import { Body } from '../../components/typography'
import { STORE_KEYS } from '../../stores'
import Progress from '../../components/progress'
import Footer from '../../components/footer';
import screen from "../../constants/Layout";
import SnackBar from "react-native-snackbar-component";

class TfaSetup extends Component {
  constructor(props) {
    super(props);

    this.state = {
      password: '',
      error: '',
      snack: false,
    }
  }

  onCopyCode = () => {
    const { dataUrl } = this.props;
    Clipboard.setString(dataUrl);
    this.setState({
      snack: true,
    });
    setTimeout(() => {
      this.setState({
        snack: false,
      });
    }, 2000)
  };

  onNext2FA = () => {
    const { navigation } = this.props;
    // add new screen here .....
    navigation.navigate('TfaVerify');
  };

  render() {
    const { navigation, dataUrl, loading } = this.props;
    const { snack } = this.state;

    const footerButtons = [
      {
        onPress: () => this.onCopyCode(),
        label: 'Copy Code'
      }, {
        onPress: () => this.onNext2FA(),
        label: 'Next',
        primary: true
      }
    ];

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Setup 2FA'} {...{ navigation }}>
          <Container>
            <Top>
              <QRWrapper>
                <QRCode
                  value={`otpauth://totp/PegNet-Mobile?secret=${dataUrl}`}
                  size={(screen.window.width || 0) * 0.7}
                />
              </QRWrapper>
              <Description>
                Please scan the QR code with Google Auth app or copy codes for manual input.
              </Description>
            </Top>

            <SnackBar
              visible={snack}
              textMessage="Copied code to Clipboard"
            />

            {/* Buttons */}
            <Footer buttons={footerButtons} />
          </Container>
        </Screen>
        { loading && <Progress done={false}/> }
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { dataUrl, loading },
     }) => ({
      dataUrl,
      loading,
    })
  )
)(TfaSetup);


// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
  margin-left: auto;
  margin-right: auto;
  text-align: center;
`

const QRWrapper = styled.View`
  display: flex;
  align-items: center;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
  text-align: center;
`
