// Dependencies
import React from 'react'
import styled from 'styled-components/native'

// Components
import { Body, Heading } from './typography'
import IconButton from './icon-button'

// Export component
export default ({ navigation, image, title, description }) => (
  <Header>
    <HeaderContent>
      <HeaderImage
        source={image}
      />
    </HeaderContent>
    <Title center>
      {title}
    </Title>
    <Description center>
      {description}
    </Description>
    <Container>
      <IconButton
        onPress={() => navigation.navigate('Send')}
        image={require('../assets/icons/send-icon.png')}
        style={{ width: 18, height: 18 }}
        text="Send"
      />

      <IconButton
        onPress={() => navigation.navigate('Modal')}
        image={require('../assets/icons/add-contact-icon.png')}
        style={{ width: 20, height: 20 }}
        text="Add contact"
      />

      <IconButton
        onPress={() => navigation.navigate('Modal')}
        image={require('../assets/icons/share-icon.png')}
        style={{ width: 20, height: 20 }}
        text="Share"
      />
    </Container>
  </Header>
)

// Styles
const Header = styled.View`
  margin-bottom: ${props => props.theme.spacing}px;
  justify-content: center;
`

const HeaderContent = styled.View`
  margin: ${props => props.theme.spacing}px auto;
  justify-content: center;
  align-items: center;
`

const Title = styled(Heading)`
  font-size: 22px;
  line-height: 30px;
`

const Description = styled(Body)`
  font-size: 16px;
  line-height: 22px;
`

const HeaderImage = styled.Image`
  width: 126px;
  height: 126px;
  border-radius: 63px;
`

const Container = styled.View`
  width: 100%;
  flex-direction: row;
  justify-content: center;
  margin-top: ${props => props.theme.spacing * .5}px;
`
