// Dependencies
import React, { Fragment, useState, useEffect } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'
import theme from '../../theme'
import { STORE_KEYS } from '../../stores'

// Components
import Screen from '../../components/screen'
import { Body } from '../../components/typography'
import PinCodeDots from '../../components/pin-code-dots'
import NumberPad from '../../components/number-pad'
import SlideUp from '../../components/animations/slide-up'
import FadeIn from '../../components/animations/fade-in'

// Constants
const PIN_LENGHT = 4

// Export component
const PinCode = ({ navigation, setPincode }) => {
  const [ready, setReady] = useState(false)
  const [pin, setPin] = useState('')
  const [err, setErr] = useState('')

  useEffect(() => {
    setReady(true)
  }, [])

  const numberPadHandler = (type, value) => {
    switch(type) {
      case 'number':
        pin.length < PIN_LENGHT && setPin(pin => pin + value)
        break;
      case 'remove':
        setPin(pin => pin.slice(0, -1))
        break;
      case 'next': {
        if (pin.length < 4) {
          setErr('Please input pin codes correctly.')
        } else {
          setErr('')
          setPincode(pin)
          navigation.navigate('OnboardingConfirm1')
        }
        break;
      }
    }
  }

  return (
    <Screen title="Create a PIN" {...{ navigation }}>
      <Container>
        <Body style={{ marginTop: theme.spacing }} center>
          Your PIN is a 4 digit code used to do quick{"\n"} re-logins into your account.
        </Body>
        {(err || '').length > 0 && (
          <Error>{ err }</Error>
        )}
        {ready && (
          <Fragment>
            <FadeIn>
              <PinCodeDots
                style={{ marginTop: theme.spacing * 2 }}
                length={PIN_LENGHT}
                value={pin}
              />
            </FadeIn>
            <Bottom>
              <SlideUp>
                <NumberPad onPress={numberPadHandler}/>
              </SlideUp>
            </Bottom>
          </Fragment>
        )}
      </Container>
    </Screen>
  )
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { setPincode },
     }) => ({
      setPincode,
    })
  )
)(PinCode);

// Styles
const Container = styled.View`
  flex: 1;
  align-items: center;
`

const Bottom = styled.View`
  margin-top: auto;
  width: 100%;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
