// Dependencies
import React, { Fragment } from 'react'
import { Platform, View } from 'react-native'
import * as Haptics from 'expo-haptics'
import styled, { css } from 'styled-components/native'
import { FontAwesome } from '@expo/vector-icons'
import theme from '../theme'

// Components
import TouchableOpacity from './touchable-opacity'
import { Body } from './typography'
import Toggle from  './toggle'

// Export section component
export const Section = ({ children, title }) => {
  return (
    <Fragment>
      {!!title && (
        <SectionTitle>{title}</SectionTitle>
      )}
      <SectionContainer>
        {React.Children.map(children, (child, index) => {
          const showSeperator = children.length > 1 && index !== children.length - 1

          return [
            child,
            showSeperator && <Seperator/>
          ]
        })}
      </SectionContainer>
    </Fragment>
  )
}

// Export item component
export const Item = ({ icon, label, type, value, onPress, onChange }) => {

  if  (type === 'toggle' && !!onChange) {
    onPress = () => {
      if (Platform.OS === 'ios' || Platform.OS === 'android') {
        Haptics.impactAsync(Haptics.ImpactFeedbackStyle.Medium)
      }
      onChange(!value)
    }
  }

  return (
    <ItemBackground>
      <ItemContainer
        as={onPress ? TouchableOpacity : null}
        {...{ onPress }}
      >
        <ItemIcon source={icon} />
        <ItemLabel>{label}</ItemLabel>
        {type === 'text' && (
          <Fragment>
            <ItemValue spaceAfter={!!onPress}>{value}</ItemValue>
            {onPress && <FontAwesome name="angle-right" size={25} color={theme.colors.light} />}
          </Fragment>
        )}
        {type === 'toggle' && (
          <View pointerEvents="none">
            <Toggle {...{ value }}/>
          </View>
        )}
      </ItemContainer>
    </ItemBackground>
  )
}

Item.defaultProps = {
  type: 'text'
}

// Styles
const SectionContainer = styled.View`
  background-color: ${props => props.theme.colors.tertiary};
  border-color: ${props => props.theme.colors.dim};
  border-top-width: 1px;
  border-bottom-width: 1px;
  margin-bottom: ${props => props.theme.spacing * 2}px;
`

const SectionTitle = styled(Body)`
  color: ${props => props.theme.colors.light};
  margin: 0 ${props => props.theme.spacing}px ${props => props.theme.spacing}px;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
`

const Seperator = styled.View`
  background-color: ${props => props.theme.colors.dim};
  height: 1px;
  margin-left: ${props => props.theme.spacing}px;
`

const ItemContainer = styled.View`
  background-color: ${props => props.theme.colors.tertiary};
  padding: 0 ${props => props.theme.spacing}px;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  height: 55px;
`

const ItemBackground = styled.View`
  background-color: ${props => props.theme.colors.background};
`

const ItemIcon = styled.Image`
  width: 18px;
  height: 18px;
  margin-right: ${props  => props.theme.spacing * .5}px;
`

const ItemLabel = styled(Body)`
  color: ${props => props.theme.colors.white};
  padding-right: ${props  => props.theme.spacing}px;
  margin-right: auto;
`

const ItemValue = styled(Body)`
  padding-left: ${props  => props.theme.spacing}px;
  ${props => props.spaceAfter && css`
    padding-right: ${props  => props.theme.spacing * .5}px;
  `};
`
