// Dependencies
import React from 'react'
import styled, { css } from 'styled-components/native'

// Components
import TouchableOpacity from './touchable-opacity'
import Gradient from './gradient'

// Export component
export default ({ children, label, before, after, large, link, primary, footer, light, ...props }) => {
  return (
    <Touchable large={large} {...props}>
      <Button as={primary ? Gradient : null} {...{ large, link }}>
        {before}
        {label && <Label {...{ large, link, footer, light }}>{label}</Label>}
        {after}
      </Button>
    </Touchable>
  )
}

// Styles
const Touchable = styled(TouchableOpacity)`
  flex: 1;
  height: ${props => props.theme.touchTargetSize}px;
  flex-basis: ${props => props.theme.touchTargetSize}px;

  ${props => props.large && css`
    height: 54px;
    flex-basis: 54px;
  `};
`

const Button = styled.View`
  flex: 1;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background-color: ${props => props.theme.colors.tertiary};
  border-radius: 5px;

  ${props => props.large && css`
    border-radius: 5px; 
  `};

  ${props => (props.primary || props.link) && css`
    background-color: transparent;
  `};
`

const Label = styled.Text`
  color: ${props => props.theme.colors.white};
  font-family: ${props => props.theme.fonts.bold};
  margin: 0 ${props => props.theme.spacing * .5}px;

  ${props => props.footer && css`
    font-size: 16px;
  `};

  ${props => props.large && css`
    font-size: 17px;
  `};

  ${props => props.small && css`
    font-size: 13px;
  `};
  
  ${props => props.link && css`
    color: ${props => props.theme.colors.light};
  `};
  
  ${props => props.light && css`
    font-family: ${props => props.theme.fonts.medium};
  `};
`
