// Dependencies
import React, { useEffect } from 'react'
import * as Font from 'expo-font'
import { FontAwesome } from '@expo/vector-icons'

// Initialize app
const init = async ({ navigation }) => {
  // Load fonts
  await Font.loadAsync({
    'galano-grotesque-medium': require('../assets/fonts/galano-grotesque-medium.otf'),
    'galano-grotesque-regular': require('../assets/fonts/galano-grotesque-regular.otf'),
    'galano-grotesque-semibold': require('../assets/fonts/galano-grotesque-semibold.otf'),
    ...FontAwesome.font
  })

  // Do "authentication" and navigate to correct screen
  navigation.navigate('Onboarding')
}

// Initial "loading" screen
export default (props) => {
  // Initialize app
  useEffect(() => { init(props) }, [])

  // Return nothing
  return null
}
