// Dependencies
import React, { useState, useCallback } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'
import { Dimensions, Image, Platform } from "react-native";
import * as Haptics from 'expo-haptics'

// Components
import Screen from '../../../components/screen'
import TextInputField from "../../../components/text-input-field";
import Progress from "../../../components/progress";
import theme from '../../../theme'
import { STORE_KEYS } from '../../../stores'
import CoinHeader from "../../../components/coin-header";
import Button from "../../../components/button";

// Export component
const AmountConfirm = ({ amountOfExchange, setAmountOfExchange, convertCoin, getBalance, navigation }) => {
  const itemData = navigation.getParam('data', {});
  const code = itemData.code || '';
  // Handle state
  const [done, setDone] = useState(false)
  const [showProgress, setShowProgress] = useState(false)
  const [error, setError] = useState('')
  let lockConfirm = false;
  const balance = getBalance(itemData.from) || 0;

  // Handle confirmation
  const onConfirm = () => {
    if (amountOfExchange <= 0) {
      setError('Enter more than 0 amount.');
      return;
    }
    if (amountOfExchange > balance) {
      setError('Your balance is insufficient for conversion.');
      return;
    }
    if (lockConfirm) return;
    lockConfirm = true;

    // Show progress
    setShowProgress(true)

    convertCoin(itemData.from, itemData.to, code)
      .then(() => {
        // Show 'done'
        setTimeout(() => {
          setDone(true);
          if (Platform.OS === 'ios' || Platform.OS === 'android') {
            setTimeout(() => Haptics.impactAsync(Haptics.ImpactFeedbackStyle.Heavy), 500);
          }
        }, 2500);

        // Navigate away
        setTimeout(() => {
          lockConfirm = false;
          navigation.navigate('Main')
        }, 5000);
      })
      .catch(err => {
        setShowProgress(false)
        setError(err)
        lockConfirm = false;
      })
  };

  return (
    <Wrapper behavior="padding" enabled>
      <Screen title={`Enter ${itemData.from} Amount`} {...{ navigation }}>
        <Container>
          <Top>
            <CoinHeader image={itemData.image} title={`${balance} ${itemData.from}`}/>
            <Label>Converting Amount</Label>
            <TextInputField
              keyboardType='numeric'
              onChangeText={setAmountOfExchange}
              value={amountOfExchange}
            />
            {(error || '').length > 0 && (
              <Error>{ error }</Error>
            )}
          </Top>
          <Bottom>
            <Button label="Confirm" onPress={() => onConfirm()} primary large/>
          </Bottom>
        </Container>
      </Screen>
      {showProgress && <Progress done={done}/>}
    </Wrapper>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         amountOfExchange,
         setAmountOfExchange,
         convertCoin,
         getBalance,
       }
     }) => ({
      amountOfExchange,
      setAmountOfExchange,
      convertCoin,
      getBalance,
    })
  )
)(AmountConfirm);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px ${props => props.theme.spacing}px 0;
  justify-content: space-between;
`

const Top = styled.View`
  margin: 0 ${props => props.theme.spacing}px;
`

const Bottom = styled.View`
  height: 50px;
  margin: 0 ${props => props.theme.spacing}px ${props => props.theme.spacing}px;
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  font-size: 12px;
  padding-top: 15px;
`;
