import React from 'react';
import { action, observable, reaction } from 'mobx';

import {
  HistoryRequest
} from '../lib/apis';
import { _retrieveData } from "../utils";

class HistoryStore {
  @observable txHistoryData = [];
  isLoggedIn = false;

  constructor(authStore) {
    this.isLoggedIn = authStore.isLoggedIn;

    if (this.isLoggedIn) {
      this.getTxHistory();
    }

    reaction(
      () => ({
        isLoggedIn: authStore.isLoggedIn
      }),
      authObj => {
        this.isLoggedIn = authObj.isLoggedIn;
        // console.log("authObj.isLoggedIn:", authObj.isLoggedIn);
        if (this.isLoggedIn) {
          this.getTxHistory();
        }
      }
    );
  }

  @action.bound async getTxHistory() {
    const authToken = await _retrieveData('authToken');
    HistoryRequest(authToken,{})
      .then(data => {
        // console.log('data:', data);
        if (!data.error) {
          try {
            const txData = data.data.result.actions || [];
            this.txHistoryData = txData;
            // console.log('this.txHistoryData:', this.txHistoryData);
          } catch (e) {
            this.txHistoryData = [];
          }
        } else {
          this.txHistoryData = [];
        }
      })
      .catch(err => {
        // console.log('getTxHistory: ', err);
        this.txHistoryData = [];
      });
  }
}

export default (authStore) => {
  const store = new HistoryStore(authStore);
  return store;
};
