// Dependencies
import React, {useState} from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../../components/screen'
import {Heading, Body} from '../../../components/typography'
import Button from '../../../components/button'
import TextInputField from '../../../components/text-input-field'
import Progress from '../../../components/progress'
import { STORE_KEYS } from '../../../stores'

// Export Component
const SignIn = ({ navigation, loginWithUsername, is2FAEnabled, loading }) => {
  const [text, setText] = useState('')
  const [pass, setPass] = useState('')
  const [err, setErr] = useState('')

  const onMoveLogin = () => {
    if ((text || '').trim().length === 0) {
      setErr('Please input username correctly.')
    } else if ((pass || '').trim().length === 0) {
      setErr('Please input password correctly.')
    } else {
      loginWithUsername(text, pass)
        .then(data => {
          if (data.success) {
            if (!data.is_tfa) {
              navigation.navigate('App')
            } else {
              navigation.navigate('TfaLogin')
            }
          } else {
            setErr('Login is failed.')
          }
        })
    }
  }

  return (
    <Wrapper behavior="padding" enabled>
      <Screen>
        <Container>
          <LogoImage
            source={require('./assets/logo-white.png')}
          />
          <Description>
            The secure and easy to use mobile wallet{"\n"} for PegNet assets
          </Description>
          <Top>
            <Label>Username</Label>
            <InputWrapper>
              <TextInputField
                onChangeText={setText}
                value={text}
              />
            </InputWrapper>
            <Label>Password</Label>
            <TextInputField
              secureTextEntry={true}
              onChangeText={setPass}
              value={pass}
            />
            {(err || '').length > 0 && (
              <Error>{ err }</Error>
            )}
          </Top>
          <ButtonWrapper>
            <Button label="Login" onPress={onMoveLogin} primary large/>
            <Button label="Create an Account" onPress={() => navigation.navigate('OnboardingStep1')} link large light/>
          </ButtonWrapper>
        </Container>
        <LabelPin>
          <Button label="PIN" onPress={() => navigation.navigate('OnboardingLogin')} link large light/>
        </LabelPin>
      </Screen>
      { loading && <Progress done={false}/> }
    </Wrapper>
  )
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: {
         loginWithUsername,
         loading,
         is2FAEnabled,
       }
     }) => ({
      loginWithUsername,
      loading,
      is2FAEnabled,
    })
  )
)(SignIn);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  align-items: center;
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .2}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
  text-align: center;
`

const ButtonWrapper = styled.View`
  width: 100%;
  margin-top: 20px;
`

const Top = styled.View`
  width: 100%;
  margin-top: auto;
`

const InputWrapper = styled.View`
  margin-bottom: 10px;
`

const LogoImage = styled.Image` 
  width: 200px;
  height: 60px;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-top: auto;
`

const LabelPin = styled.View`
  position: absolute;
  top: ${props => props.theme.spacing * .5}px;
  left: ${props => props.theme.spacing * .5}px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 10px;  
`;
