// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'

// Components
import Screen from '../../components/screen'
import Button from '../../components/button'
import TextInputField from '../../components/text-input-field'
import { Body } from '../../components/typography'

// Export component
export default ({ navigation }) => {
  const item = navigation.getParam('item', {
    label: '',
    value: '',
    title: '',
    subtitle: '',
    description: ''
  })
  const { value, title, subtitle, description } = item
  const [text, setText] = useState(value)

  return (
    <Wrapper behavior="padding" enabled>
      <Screen title={title} {...{ navigation }}>
        <Container>
          <Top>
            <Label>{subtitle}</Label>
            <TextInputField
              onChangeText={setText}
              value={text}
            />
            <Description>
              {description}
            </Description>
          </Top>
          <Bottom>
            <Button label="Save" primary large style={{ width: 160 }} />
          </Bottom>
        </Container>
      </Screen>
    </Wrapper>
  )
}

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .8}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`
