// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'
import QRCode from 'react-native-qrcode-svg'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'
import {Clipboard, Platform} from 'react-native'
import SnackBar from 'react-native-snackbar-component'

// Components
import Screen from '../../components/screen'
import ScrollView from '../../components/scroll-view'
import Footer from '../../components/footer'
import screen from '../../constants/Layout';
import { STORE_KEYS } from '../../stores'
import coinPeg from '../../assets/coins/icon_coin_peg.png'

// Export component
const Receipt = ({ fctAddr, navigation }) => {
  const itemData = navigation.getParam('data', {});
  const [snack, setSnack] = useState(false)
  const isMobile = Platform.OS === 'ios' || Platform.OS === 'android';

  const footerButtons = [
    {
      onPress: () => navigation.navigate('Modal'),
      label: 'Share'
    }, {
      onPress: () => {
        Clipboard.setString(fctAddr || '');
        setSnack(true)
        setTimeout(() => {
          setSnack(false)
        }, 2000)
      },
      label: 'Copy Address',
      primary: true
    }
  ]

  return (
    <Screen title="Receive" {...{ navigation }}>
      <Container>
        <ScrollView hasFooter>
          <Wrapper>
            <QRCode
              value={fctAddr || 'Loading'}
              size={(screen.window.width || 0) * (isMobile ? 0.7 : 0.2)}
              logo={itemData.image || coinPeg}
            />
          </Wrapper>
          <LabelAddress>
            YOUR RECEIVE ADDRESS is {fctAddr}
          </LabelAddress>
        </ScrollView>

        <SnackBar
          visible={snack}
          textMessage="Copied address to Clipboard"
        />
        {/* Buttons */}
        <Footer buttons={footerButtons} />
      </Container>
    </Screen>
  )
}

export default compose(
  inject(STORE_KEYS.REALDEPOSITSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.REALDEPOSITSTORE]: {
         fctAddr,
       }
     }) => ({
      fctAddr,
    })
  )
)(Receipt);

// Styles
const Container = styled.View`
  flex: 1;
`

const Wrapper = styled.View`
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
`

const LabelAddress = styled.Text`
  text-align: center;
  color: #fff;
  font-size: 16px;
  line-height: 30px;
  padding: 0 20px;
`
