// Dependencies
import React, { useState, Component } from 'react'
import styled from 'styled-components/native'
import { compose, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

// Components
import Screen from '../../../components/screen'
import Button from '../../../components/button'
import TextInputField from '../../../components/text-input-field'
import { Body } from '../../../components/typography'
import { STORE_KEYS } from '../../../stores'

// Export component
class Step3 extends Component {
  constructor(props) {
    super(props);

    this.state = {
      error: '',
      isValid: true,
    }
  }

  onNextStep = () => {
    const { navigation, password, password02 } = this.props
    if (password === password02 && (password || '').trim().length > 0) {
      this.setState({
        isValid: true,
        error: '',
      })
      navigation.navigate('OnboardingPinCode')
    } else if (password !== password02) {
      this.setState({
        isValid: false,
        error: "Password doesn't match."
      })
    } else {
      this.setState({
        isValid: false,
        error: "Please input password correctly."
      })
    }
  }

  render() {
    const { error, isValid } = this.state
    const {
      navigation, password, password02, setPassword, setPassword02,
    } = this.props

    return (
      <Wrapper behavior="padding" enabled>
        <Screen title={'Step 3/3'} {...{ navigation }}>
          <Container>
            <Top>
              <Label>Password</Label>
              <InputWrapper>
                <TextInputField
                  secureTextEntry={true}
                  onChangeText={setPassword}
                  value={password}
                />
              </InputWrapper>
              <Label>Confirm Password</Label>
              <TextInputField
                secureTextEntry={true}
                onChangeText={setPassword02}
                value={password02}
              />
              {!isValid && (
                <Error>{ error }</Error>
              )}
              <Description>
                Your password must have at least 10 characters, 1 lowercase letter, 1 uppercase letter, 1 number.
              </Description>
            </Top>
            <Bottom>
              <Button label="Next" onPress={this.onNextStep} primary large style={{ width: '100%' }} />
            </Bottom>
          </Container>
        </Screen>
      </Wrapper>
    )
  }
}

export default compose(
  inject(STORE_KEYS.AUTHORITYSTORE),
  observer,
  withProps(
    ({
       [STORE_KEYS.AUTHORITYSTORE]: { password, password02, setPassword, setPassword02 },
     }) => ({
      password,
      password02,
      setPassword,
      setPassword02,
    })
  )
)(Step3);

// Styles
const Wrapper = styled.KeyboardAvoidingView`
  flex: 1;
`

const Container = styled.View`
  flex: 1;
  margin: ${props => props.theme.spacing}px;
  justify-content: space-between;
`

const Top = styled.View`
`

const InputWrapper = styled.View`
  margin-bottom: 10px;
`

const Label = styled.Text`
  color: ${props => props.theme.colors.light};
  font-family: ${props => props.theme.fonts.medium};
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: ${props => props.theme.spacing * .2}px;
`

const Description = styled(Body)`
  line-height: 20px;
  margin-top: ${props => props.theme.spacing * .8}px;
`

const Bottom = styled.View`
  align-items: center;
  height: 54px;
`

const Error = styled.Text`
  color: ${props => props.theme.colors.pink2};
  padding-top: 15px;  
`;
