import React from 'react';
import { action, observable, reaction } from 'mobx';
import { Updates } from 'expo';

import {
  DepositAddressRequest,
  WithdrawCoinRequest,
  ConversionRequest,
  TxVerify,
} from '../lib/apis';
import { _retrieveData } from '../utils';

class RealDepositStore {
  @observable userId = '';
  @observable fctAddr = '';
  @observable priceUpdatedAt = '';
  @observable myBalances = {};

  @observable amountOfSend = 0;
  @observable coinOfSend = '';
  @observable addressOfSend = '';
  @observable amountOfExchange = '';

  isLoggedIn = false;
  _pollingInt = null;
  authStoreRef = null;

  constructor(authStore, coinPriceStore) {
    this.isLoggedIn = authStore.isLoggedIn;
    this.authStoreRef = authStore;

    if (this.isLoggedIn) {
      this.myBalances = [];
      this.getDepositAddresses();
    }

    reaction(
      () => ({
        isLoggedIn: authStore.isLoggedIn
      }),
      authObj => {
        this.isLoggedIn = authObj.isLoggedIn;
        this.myBalances = [];
        // console.log("authObj.isLoggedIn:", authObj.isLoggedIn);
        if (this.isLoggedIn) {
          this.getDepositAddresses();
        }
      }
    );

    reaction(
      () => ({
        maketPriceData: coinPriceStore.maketPriceData
      }),
      (marketObj) => {
        this.priceUpdatedAt = (new Date()).getTime();
      }
    );
  }

  polling = async () => {
    const authToken = await _retrieveData('authToken');
    if (!authToken) {
      clearInterval(this._pollingInt);
      return;
    }
    DepositAddressRequest(authToken, {})
      .then(data => {
        // console.log('data:', data);
        if (!data.error) {
          this.userId = data.userId || '';
          this.fctAddr = data.address;
          this.myBalances = {
            PEG: data.PEG || 0,
            pADA: data.pADA || 0,
            pATOM: data.pATOM || 0,
            pAUD: data.pAUD || 0,
            pBAT: data.pBAT || 0,
            pBNB: data.pBNB || 0,
            pBRL: data.pBRL || 0,
            pCAD: data.pCAD || 0,
            pCHF: data.pCHF || 0,
            pCNY: data.pCNY || 0,
            pDASH: data.pDASH || 0,
            pDCR: data.pDCR || 0,
            pEOS: data.pEOS || 0,
            pETH: data.pETH || 0,
            pEUR: data.pEUR || 0,
            pFCT: data.pFCT || 0,
            pGBP: data.pGBP || 0,
            pHKD: data.pHKD || 0,
            pINR: data.pINR || 0,
            pJPY: data.pJPY || 0,
            pKRW: data.pKRW || 0,
            pLINK: data.pLINK || 0,
            pLTC: data.pLTC || 0,
            pMXN: data.pMXN || 0,
            pNOK: data.pNOK || 0,
            pNZD: data.pNZD || 0,
            pPHP: data.pPHP || 0,
            pRUB: data.pRUB || 0,
            pRVN: data.pRVN || 0,
            pSEK: data.pSEK || 0,
            pSGD: data.pSGD || 0,
            pTRY: data.pTRY || 0,
            pUSD: data.pUSD || 0,
            pXAG: data.pXAG || 0,
            pXAU: data.pXAU || 0,
            pXBC: data.pXBC || 0,
            pXBT: data.pXBT || 0,
            pXLM: data.pXLM || 0,
            pXMR: data.pXMR || 0,
            pXTZ: data.pXTZ || 0,
            pZAR: data.pZAR || 0,
            pZEC: data.pZEC || 0,
          };
        } else {
          // console.log('getWAddress: ', data.error);
          this.fctAddr = '';
        }
      })
      .catch(async (err) => {
        // console.log('getWAddress: ', err);
        this.fctAddr = '';
        try {
          if (err.tokenStatus === 301 && this.authStoreRef) {
            await this.authStoreRef.logout();
            Updates.reload();
          }
        } catch (e) {
          // console.log('[resetting token is failed]');
        }
      });
  };

  @action.bound getBalance(coin) {
    return this.myBalances[coin] || 0;
  }

  getDepositAddresses = async () => {
    this.polling();
    clearInterval(this._pollingInt);
    this._pollingInt = setInterval(this.polling, 60 * 1000);
  };

  @action.bound verifyTx(code) {
    return new Promise(async (resolve, reject) => {
      const authToken = await _retrieveData('authToken');
      TxVerify(authToken, code)
        .then(data => {
          if (data.success) {
            resolve(true);
          } else {
            resolve(false);
          }
        })
        .catch(err => {
          reject("Transaction is failed.");
        })
    });
  }

  @action.bound sendCoin(code) {
      const amount = this.amountOfSend;
      const symbol = this.coinOfSend;
      const address = this.addressOfSend;

      return new Promise(async (resolve, reject) => {
        if((this.myBalances[symbol] || 0) <= 0) {
          reject(`Your ${symbol} balance is insufficient.`);
        } else if (amount <= 0) {
          reject("Please input more than 0 amount.");
        } else if (address === '') {
          reject("Please input withdrawal address.");
        } else{
          const authToken = await _retrieveData('authToken');
          WithdrawCoinRequest(authToken, {
            amount, address, symbol, code,
          })
            .then(data => {
              // console.log('data:', data);
              if (!data.error) {
                this.showPopMsg(data.message);
              } else {
                this.showPopMsg( "Withdraw request is failed.");
              }
              resolve(true);
            })
            .catch(err => {
              reject("Transaction is failed.");
            })
          }
      });
  }

  @action.bound async convertCoin(symbol, to, code) {
    const amount = this.amountOfExchange;
    return new Promise(async (resolve, reject) => {
      if((this.myBalances[symbol] || 0) <= 0) {
        reject(`Your ${symbol} balance is insufficient.`);
      } else if (amount <= 0) {
        reject("Please input more than 0 amount.");
      } else if (to === '') {
        reject("Please select target coin.");
      } else{
        const authToken = await _retrieveData('authToken');
        ConversionRequest(authToken,{
          amount,
          from: symbol,
          to,
          code,
        })
          .then(data => {
            // console.log('data:', data);
            if (!data.error) {
              this.showPopMsg(data.message);
            } else {
              this.showPopMsg( "Conversion request is failed.");
            }
            resolve(true);
          })
          .catch(err => {
            reject("Conversion is failed.");
          });
      }
    });
  }

  @action.bound setAmountCoin(amount, coin) {
    this.amountOfSend = amount;
    this.coinOfSend = coin;
  }

  @action.bound setAddressOfSend(address) {
    this.addressOfSend = address;
  }

  @action.bound setAmountOfExchange(amount) {
    this.amountOfExchange = amount;
  }

  @action.bound showPopMsg(msg) {
    // console.log(msg);
  }
}


export default (authStore, coinPriceStore) => {
  const store = new RealDepositStore(authStore, coinPriceStore);
  return store;
};
