// Dependencies
import React, { useState } from 'react'
import styled from 'styled-components/native'
import { Ionicons } from '@expo/vector-icons'
import numeral from 'numeral'
import theme from '../../theme'

// Components
import TouchableOpacity from '../touchable-opacity'
import TransactionItem from '../transaction-item'
import { Body } from '../typography'
import NumberPad from '../number-pad'
import SlideUp from '../animations/slide-up'
import FadeIn from '../animations/fade-in'
import EnterAmount from '../enter-amount'

// Constants
const MAX_AMOUNT_LENGTH = 8

// Create parts from amount
const getPartsFromAmount = amount => {
  return amount.toString().split('')
}

// Get amount from parts
const getAmountFromParts = parts => {
  return parseFloat(parts.join('') || 0)
}

// Format amount parts
const getFormattedAmountFromParts = parts => {
  // Get integer and fractional parts
  let [integer, fractional = false] = parts.join('').split('.')

  // Prepare output
  const output = []

  // Add integer part
  output.push(numeral(integer).format('0,0'))

  // Add fractional part
  fractional !== false && output.push(fractional)

  // Return formatted amount
  return output.join('.')
}

// Export component
export default ({ onRecipient, button = 'Enter', onNext, recipient, onRemoveRecipient, itemData = null }) => {
  const [amountParts, setAmountParts] = useState([])
  const hasDecimalPoint = amountParts.includes('.')
  const key = itemData.key || 'PEG'
  const coinAmount = itemData.coinAmount || ''
  const coinUSDPrice = itemData.coinUSDPrice || 0

  const onBalance = () => {
    const total = 0
    setAmountParts(getPartsFromAmount(total))
  }

  const getUsdValue = () => {
    const value = getAmountFromParts(amountParts)
    return numeral(value * coinUSDPrice).format('0,0[.]00')
  }

  const numberPadHandler = (type, value) => {
    switch(type) {
      // Handle numbers
      case 'number':
        // Refuse multiple leading zero's
        if (amountParts.length === 1 && amountParts[0] === 0 && value === 0) {
          break
        }

        // Refuse to exceed max length
        if (amountParts.length >= (hasDecimalPoint ? MAX_AMOUNT_LENGTH + 1 : MAX_AMOUNT_LENGTH)) {
          break
        }

        // Add value to parts
        setAmountParts(parts => [...parts, value])

        // Break
        break

      // Handle decimal separator
      case 'separator':
        // Refuse multiple decimal separators
        if (amountParts.includes('.')) {
          break
        }

        // Refuse more than max length
        if (amountParts.length >= (MAX_AMOUNT_LENGTH - 1)) {
          break
        }

        // Add separator
        setAmountParts(parts => [
          ...(parts.length === 0 ? [0] : parts),
          '.'
        ])

        // Break
        break

      // Handle next button
      case 'next':
        onNext && onNext({
          amount: getAmountFromParts(amountParts),
          usdAmount: getUsdValue(getAmountFromParts(amountParts)),
          coin: key,
        })
        break
    }
  }

  return (
    <Container>
      <Top>
        <FadeIn>
          <EnterAmount
            amount={getFormattedAmountFromParts(amountParts)}
            unit={key}
            inactive={amountParts.length === 0}
            onDelete={() => {
              setAmountParts(parts => parts.slice(0,-1))
            }}
          />
          <TouchableOpacity onPress={onBalance} style={{ marginTop: theme.spacing }}>
            <Body center>
              Available {coinAmount}
            </Body>
          </TouchableOpacity>
          {/* amountParts.length > 0 && (
            <Body style={{ marginTop: theme.spacing * 0.1 }} center>
              ~ {getUsdValue()} USD
            </Body>
          ) */}
        </FadeIn>
      </Top>
      <Bottom>
        <SlideUp>
          {!!recipient && (
            <RecipientContainer>
              <TransactionItem {...recipient}/>
              <RemoveIcon onPress={() => onRemoveRecipient()}>
                <Ionicons name="ios-close" size={42} color="white" />
              </RemoveIcon>
            </RecipientContainer>
          )}
          <NumberPad
            type="decimal"
            onPress={numberPadHandler}
            button={button}
          />
        </SlideUp>
      </Bottom>
    </Container>
  )
}

// Styles
const Container = styled.View`
  flex: 1;
`

const Top = styled.View`
  margin-top: ${props => props.theme.spacing * 2}px;
`

const Bottom = styled.View`
  margin-top: auto;
`

const RecipientContainer = styled.View`
  position: relative;
  padding: 0 ${props => props.theme.spacing}px;
  margin-bottom: ${props => props.theme.spacing}px;
`

const RemoveIcon = styled(TouchableOpacity)`
  position: absolute;
  right: ${props => props.theme.spacing}px;
  width: ${props => props.theme.touchTargetSize}px;
  height: ${props => props.theme.touchTargetSize}px;
  justify-content: center;
  align-items: center;
`
